// src/components/Banner.jsx
import React, { useState, useEffect } from 'react';
import './Banner.css';

const Banner = () => {
  const [validationResult, setValidationResult] = useState({});
  const [diasRestantes, setDiasRestantes] = useState(null);

  const calcularDiasRestantes = (fechaString) => {
    if (!fechaString) return null;
    
    try {
      const [dia, mes, ao] = fechaString.split('/');
      const fechaVencimiento = new Date(ao, mes - 1, dia);
      const hoy = new Date();
      
      fechaVencimiento.setHours(23, 59, 59, 999);
      hoy.setHours(0, 0, 0, 0);
      
      const diferenciaTiempo = fechaVencimiento.getTime() - hoy.getTime();
      const diasDiferencia = Math.ceil(diferenciaTiempo / (1000 * 3600 * 24));
      
      return diasDiferencia;
    } catch (error) {
      return null;
    }
  };

  const validarEquipo = async () => {
    try {
      if (window.electronAPI?.validarEquipo) {
        const result = await window.electronAPI.validarEquipo();
        setValidationResult(result);
        
        // Si ok es false, cerrar WhatsApp automticamente
        if (result?.ok === false && window.electronAPI?.cerrarWhatsapp) {
          try {
            await window.electronAPI.cerrarWhatsapp();
            console.log('WhatsApp cerrado debido a licencia invlida');
          } catch (error) {
            console.error('Error al cerrar WhatsApp:', error);
          }
        }
        
        if (result?.ok && result?.fecha) {
          setDiasRestantes(calcularDiasRestantes(result.fecha));
        }
      }
    } catch (error) {
      setValidationResult({ ok: false });
    }
  };

  const handleCerrarCuenta = async () => {
    try {
      if (window.electronAPI?.cerrarCuenta) {
        await window.electronAPI.cerrarCuenta();
        console.log('Cuenta cerrada exitosamente');
      }
    } catch (error) {
      console.error('Error al cerrar cuenta:', error);
    }
  };

  const handleClick = () => {
    const url = 'https://wai.artechclick.com';
    if (window.electronAPI?.openExternalLink) {
      window.electronAPI.openExternalLink(url);
    } else {
      window.open(url, '_blank');
    }
  };

  useEffect(() => {
    validarEquipo();
    const interval = setInterval(validarEquipo, 100000);
    return () => clearInterval(interval);
  }, []);

  // Determinar estado
  let estado = 'loading';
  if (validationResult?.ok === false) estado = 'blocked';
  else if (validationResult?.ok === true && diasRestantes !== null) {
    if (diasRestantes < 0) estado = 'expired';
    else if (diasRestantes > 370) estado = 'vitalicio';
    else estado = diasRestantes <= 3 ? 'warning' : 'success';
  }

  const getContent = () => {
    switch (estado) {
      case 'blocked':
        return {
          icon: '',
          title: 'Debes renovar tu licencia',
          subtitle: 'Tu cuenta no est autorizada para usar este programa',
          message: 'Para poder utilizar el Agente IA WhatsApp, necesitas:\n\n Registrarte en el sistema de facturacin\n Activar tu cuenta con una licencia vlida\n Verificar que tu equipo est autorizado\n\nSin una licencia activa, no podrs acceder a las funcionalidades del programa.',
          button: 'Activar cuenta ahora'
        };
      case 'expired':
        return {
          icon: '',
          title: 'Licencia vencida',
          subtitle: `Venci el ${validationResult.fecha}`,
          message: 'Tu licencia ha expirado. Para continuar usando el programa, debes renovar tu licencia inmediatamente.',
          button: 'Renovar licencia ahora'
        };
      case 'error':
        return {
          icon: '',
          title: 'Licencia invlida',
          subtitle: 'Acceso no autorizado',
          button: 'Renovar'
        };

      case 'vitalicio':
          return {
          icon: '',
          title: 'Licencia Vitalicia',
          subtitle: 'Acceso de por vida',
          button: 'Ver beneficios'
          };
      case 'warning':
        return {
          icon: '',
          title: `Licencia vence en ${diasRestantes} ${diasRestantes === 1 ? 'da' : 'das'}`,
          subtitle: `Hasta: ${validationResult.fecha}`,
          button: 'Renovar +30 das'
        };
      case 'success':
        return {
          icon: '',
          title: `Licencia - ${diasRestantes} das restantes`,
          subtitle: `Hasta: ${validationResult.fecha}`,
          button: 'Extender +30 das'
        };
      default:
        return {
          icon: '',
          title: 'Licencia - Verificando...',
          subtitle: 'Consultando estado',
          button: 'Cargando...'
        };
    }
  };

  const content = getContent();

  // Si la licencia est bloqueada o vencida, mostrar overlay completo
  if (estado === 'blocked' || estado === 'expired') {
    return (
      <div className="banner-overlay">
        <div className={`banner-fullscreen ${estado}`}>
          <div className="banner-fullscreen-content">
            <div className="banner-fullscreen-icon">{content.icon}</div>
            <h2 className="banner-fullscreen-title">{content.title}</h2>
            <p className="banner-fullscreen-subtitle">{content.subtitle}</p>
            <div className="banner-fullscreen-message">
              {content.message.split('\n').map((line, index) => (
                <p key={index}>{line}</p>
              ))}
            </div>
            <div className="banner-fullscreen-buttons">
              <button className="banner-fullscreen-button primary" onClick={handleClick}>
                {content.button}
              </button>
              <button className="banner-fullscreen-button secondary" onClick={handleCerrarCuenta}>
                Cerrar cuenta
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={`banner ${estado}`} onClick={handleClick}>
      <div className="banner-info">
        <div className="banner-main">
          <span className="banner-icon">{content.icon}</span>
          <span className="banner-title">{content.title}</span>
        </div>
        <div className="banner-sub">{content.subtitle}</div>
      </div>
      <div className="banner-action">
        <span className="banner-button">{content.button}</span>
      </div>
    </div>
  );
};

export default Banner;