// src/App.jsx
import React, { useState, useEffect } from 'react';
import WhatsApp from './components/WhatsApp';
import PredefinedMessages from './components/PredefinedMessages';
import Tutoriales from './components/Tutoriales';
import MensajeInicial from './components/MensajeInicial';
import MensajeImagen from './components/MensajeImagen';
import Cuenta from './components/Cuenta';
import Banner from './components/Banner';
import Clientes from './components/Clientes';
import Agentes from './Agentes';
import Reventa from './components/Reventa';
import Remarketing from './components/Remarketing';
// import Configuracion from './components/Configuracion';
import Agenda from './components/Agenda';
import './App.css';
import './mensajes.css';
import { ValidadorProvider, useValidador } from './Validador';
import Condiciones from './components/Condiciones';
import { TestigoProvider } from './Testigo';

// Importar iconos de lucide-react
import { 
  Home, 
  MessageSquare, 
  FileText, 
  Image, 
  Users, 
  BookOpen, 
  DollarSign,
  CreditCard,
  RotateCcw,
  Settings,
  Calendar
} from 'lucide-react';

// Componente principal que proporciona el ValidadorProvider
function App() {
  return (
    <ValidadorProvider>
      <AppContent />
    </ValidadorProvider>
  );
}

// Componente que contiene toda la lgica de la aplicacin
function AppContent() {
  const [activeSection, setActiveSection] = useState('mainFunctions');
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [claveApp, setClaveApp] = useState(0);

  // Hook del validador para manejar condiciones
  const { updateConditionsAccepted } = useValidador();

  // Handler para cuando las condiciones son aceptadas
  const handleConditionsAccepted = () => {
    console.log(' [App] Conditions accepted - updating global state...');
    updateConditionsAccepted(true);
    // Forzar re-renderizado completo de la app
    setClaveApp(prev => prev + 1);
  };

  const showContentSection = (sectionId) => {
    setActiveSection(sectionId);
    setSidebarOpen(false);
  };

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  // Manejar cambio de agente
  const manejarCambioAgente = (nuevoAgente) => {
    setClaveApp(prev => prev + 1);
  };

  // Verificar disponibilidad de electronAPI al montar
  useEffect(() => {
    if (!window.electronAPI) {
      console.error('electronAPI no est disponible. Asegrate de ejecutar la aplicacin desde Electron.');
    } else {
      console.log('electronAPI disponible y listo');
    }
  }, []);

  const renderContent = () => {
    switch(activeSection) {
      case 'mainFunctions':
        return <WhatsApp />;
      case 'MensajeInicial':
        return <MensajeInicial />;
      case 'MensajeImagen':
        return <MensajeImagen />;
      case 'predefinedMessages':
        return <PredefinedMessages />;
      case 'clientes':
        return <Clientes />;
      case 'tutoriales':
        return <Tutoriales />;
      case 'remarketing':
        return <Remarketing />;
      case 'cuenta':
        return <Cuenta />;
      case 'reventa':
        return <Reventa />;
      case 'agenda': 
        return <Agenda />;

      default:
        return <WhatsApp />;
    }
  };

  return (
    <>
      <Condiciones onConditionsAccepted={handleConditionsAccepted} />
      <TestigoProvider>
        <div key={claveApp} className="App">
          {/* Banner promocional persistente */}
          <Banner />

          <div className="app-wrapper">
            {/* Overlay para cerrar sidebar al hacer clic fuera */}
            {sidebarOpen && (
              <div 
                className="overlay visible"
                onClick={() => setSidebarOpen(false)}
              />
            )}

            {/* Sidebar */}
            <div id="sidebar" className={`sidebar ${sidebarOpen ? 'open' : ''}`}>
              <button 
                id="closeSidebarBtn" 
                className="close-sidebar-btn"
                onClick={() => setSidebarOpen(false)}
              >
                &times;
              </button>
              <div className="sidebar-menu">
                <div className="menu-items-top">
                  <button 
                    className={`menu-item ${activeSection === 'mainFunctions' ? 'active' : ''}`}
                    data-section="mainFunctions"
                    onClick={() => showContentSection('mainFunctions')}
                  >
                    <Home size={20} />
                    <span>Funciones Principales</span>
                  </button>
                  <button 
                    className={`menu-item ${activeSection === 'MensajeInicial' ? 'active' : ''}`}
                    data-section="MensajeInicial"
                    onClick={() => showContentSection('MensajeInicial')}
                  >
                    <MessageSquare size={20} />
                    <span>Mensaje Inicial</span>
                  </button>

                  <button 
                    className={`menu-item ${activeSection === 'predefinedMessages' ? 'active' : ''}`}
                    data-section="predefinedMessages"
                    onClick={() => showContentSection('predefinedMessages')}
                  >
                    <FileText size={20} />
                    <span>Mensajes Predeterminados</span>
                  </button>
                  <button 
                    className={`menu-item ${activeSection === 'MensajeImagen' ? 'active' : ''}`}
                    data-section="MensajeImagen"
                    onClick={() => showContentSection('MensajeImagen')}
                  >
                    <Image size={20} />
                    <span>Reconocer imgenes</span>
                  </button>
                  <button 
                    className={`menu-item ${activeSection === 'clientes' ? 'active' : ''}`}
                    data-section="clientes"
                    onClick={() => showContentSection('clientes')}
                  >
                    <Users size={20} />
                    <span>Gestin de Clientes</span>
                  </button>

                  <button 
                    className={`menu-item ${activeSection === 'tutoriales' ? 'active' : ''}`}
                    data-section="tutoriales"
                    onClick={() => showContentSection('tutoriales')}
                  >
                    <BookOpen size={20} />
                    <span>Tutoriales</span>
                  </button>
                  <button 
                    className={`menu-item ${activeSection === 'remarketing' ? 'active' : ''}`}
                    data-section="remarketing"
                    onClick={() => showContentSection('remarketing')}
                  >
                    <RotateCcw size={20} />
                    <span>Remarketing</span>
                  </button>
                  <button 
                    className={`menu-item ${activeSection === 'reventa' ? 'active' : ''}`}
                    data-section="reventa"
                    onClick={() => showContentSection('reventa')}
                  >
                    <DollarSign size={20} />
                    <span><b>Revender y Ganar</b></span>
                  </button>

                  <button 
                    className={`menu-item ${activeSection === 'agenda' ? 'active' : ''}`}
                    data-section="agenda"
                    onClick={() => showContentSection('agenda')}
                  >
                    <Calendar size={20} />
                    <span>Agenda</span>
                  </button>


                </div>
                
                <div className="menu-items-bottom">
                  <button 
                    className={`menu-item menu-item-premium ${activeSection === 'cuenta' ? 'active' : ''}`}
                    data-section="cuenta"
                    onClick={() => showContentSection('cuenta')}
                  >
                    <CreditCard size={20} className="premium-icon" />
                    <span className="premium-text">
                      <span className="premium-title">Cuenta</span>
                      <span className="premium-subtitle">Facturacin</span>
                    </span>
                    <span className="premium-arrow"></span>
                  </button>
                </div>
              </div>
            </div>

            {/* rea de contenido principal */}
            <div id="mainContentArea" className="main-content-area">
              <button 
                id="toggleSidebarBtn" 
                className="toggle-sidebar-btn"
                onClick={toggleSidebar}
              >
                &#9776;
              </button>
              
              {/* Contenido dinmico */}
              {renderContent()}
            </div>
          </div>

          {/* Agentes flotante */}
          <Agentes alCambiarAgente={manejarCambioAgente} />
        </div>
      </TestigoProvider>
    </>
  ); 
}

export default App;