// src/Testigo.jsx
import React, { createContext, useContext, useState, useEffect } from 'react';

// Crear el contexto para el estado del WhatsApp
const TestigoContext = createContext();

// Hook personalizado para usar el contexto del Testigo
export const useTestigo = () => {
  const context = useContext(TestigoContext);
  if (!context) {
    throw new Error('useTestigo debe ser usado dentro de un TestigoProvider');
  }
  return context;
};

// Provider del contexto del Testigo
export const TestigoProvider = ({ children }) => {
  // Estados para el indicador de WhatsApp (ahora globales)
  const [whatsappStatus, setWhatsappStatus] = useState('Inactivo');
  const [statusLight, setStatusLight] = useState('inactive');
  const [isWhatsappConnected, setIsWhatsappConnected] = useState(false); // Nuevo estado para indicar conexin

  // Funcin para actualizar el estado de WhatsApp (movida aqu)
  const updateWhatsappStatusIndicator = (status) => {
    let displayStatusText = '';
    let lightClass = 'inactive';
    let connected = false;

    if (status.includes('listo') || status.includes('Autenticado') || 
        status.includes('Conectado') || status.includes('Mensaje')) {
      lightClass = 'active';
      displayStatusText = 'Activo';
      connected = true;
    } else if (status.includes('Iniciando')) {
      lightClass = 'inactive';
      displayStatusText = 'Iniciando...';
      connected = false;
    } else if (status.includes('Esperando QR')) {
      lightClass = 'inactive';
      displayStatusText = 'Esperando QR';
      connected = false;
    } else if (status.includes('Cerrando') || status.includes('Reiniciando')) {
      lightClass = 'inactive';
      displayStatusText = 'Procesando...';
      connected = false;
    } else if (status.includes('Desconectado') || status.includes('Fallo autenticacion') || 
               status.includes('Error')) {
      lightClass = 'inactive';
      displayStatusText = 'Inactivo';
      connected = false;
    } else {
      lightClass = 'inactive';
      displayStatusText = 'Inactivo';
      connected = false;
    }

    setWhatsappStatus(displayStatusText);
    setStatusLight(lightClass);
    setIsWhatsappConnected(connected); // Actualiza el estado de conexin
  };

  // Effect para configurar listener de Electron al montar el TestigoProvider
  useEffect(() => {
    console.log('[TestigoProvider] Component mounted, configurando listeners...');
    
    if (!window.electronAPI) {
      console.error('[TestigoProvider] electronAPI no est disponible. No se podrn recibir actualizaciones de WhatsApp.');
      // Opcional: Establecer un estado de error si electronAPI no est presente
      updateWhatsappStatusIndicator('Error de conexin');
      return;
    }

    // Configurar listener de actualizaciones de WhatsApp
    console.log('[TestigoProvider] Configurando listener de WhatsApp status updates...');
    const removeListener = window.electronAPI.onWhatsappStatusUpdate((update) => {
      console.log('[TestigoProvider] Recibida actualizacin de WhatsApp:', update);
      // Solo actualizamos el estado interno del testigo, el QR se maneja en WhatsApp.jsx
      if (update.type === 'status' || update.type === 'message') {
        updateWhatsappStatusIndicator(update.data);
      } else if (update.type === 'qr') {
        // Para QR, el Testigo solo actualiza el estado, WhatsApp.jsx sigue mostrando el QR
        updateWhatsappStatusIndicator('Esperando QR');
      }
    });

    // Cleanup function
    return () => {
      console.log('[TestigoProvider] Limpiando listeners de Testigo component');
      if (removeListener && typeof removeListener === 'function') {
        removeListener();
      }
    };
  }, []); // Se ejecuta solo una vez al montar

  // Valor del contexto que se proveer
  const contextValue = {
    whatsappStatus,
    statusLight,
    isWhatsappConnected, // Exponer el estado de conexin
    updateWhatsappStatusIndicator, // Exponer la funcin para que WhatsApp.jsx pueda usarla para la UI
  };

  return (
    <TestigoContext.Provider value={contextValue}>
      {children}
      {/* Indicador de estado de WhatsApp global y persistente */}
      <div id="whatsappStatusIndicator" className="whatsapp-status-indicator">
        <span className={`status-light ${statusLight}`}></span>
        <span className="status-text">WhatsApp: {whatsappStatus}</span>
      </div>
    </TestigoContext.Provider>
  );
};