// src/components/MensajeInicial.jsx
import React, { useState, useEffect, useRef } from 'react';
import './MensajeInicial.css';

const MensajeInicial = () => {
    const [agente, setAgente] = useState('1');
    const [mensajeTexto, setMensajeTexto] = useState('');
    const [archivos, setArchivos] = useState([]);
    const [activo, setActivo] = useState(false);
    const [cargando, setCargando] = useState(true);
    const [guardando, setGuardando] = useState(false);
    const [mensaje, setMensaje] = useState({ texto: '', tipo: '' });
    const fileInputRef = useRef(null);

    // Cargar datos iniciales
    useEffect(() => {
        cargarDatos();
    }, []);

    const cargarDatos = async () => {
        setCargando(true);
        try {
            // Obtener agente actual
            const agenteActual = await window.electronAPI.getAgente();
            setAgente(agenteActual);

            // Obtener mensaje inicial para este agente
            const datosIniciales = await window.electronAPI.getMensajeInicial();
            
            if (datosIniciales && datosIniciales.mensaje) {
                setMensajeTexto(datosIniciales.mensaje.text || '');
                setArchivos(datosIniciales.mensaje.files || []);
                setActivo(datosIniciales.activo || false);
            } else {
                // Valores por defecto si no hay datos
                setMensajeTexto('');
                setArchivos([]);
                setActivo(false);
            }
        } catch (error) {
            console.error('Error al cargar datos:', error);
            mostrarMensaje('Error al cargar los datos', 'error');
        } finally {
            setCargando(false);
        }
    };

    const mostrarMensaje = (texto, tipo) => {
        setMensaje({ texto, tipo });
        setTimeout(() => {
            setMensaje({ texto: '', tipo: '' });
        }, 3000);
    };

    const manejarSeleccionArchivos = (event) => {
        const files = Array.from(event.target.files);
        
        // Filtrar archivos vlidos para WhatsApp
        const archivosValidos = files.filter(file => {
            const tiposPermitidos = [
                'image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp',
                'video/mp4', 'video/avi', 'video/mov', 'video/wmv', 'video/flv',
                'audio/mp3', 'audio/wav', 'audio/ogg', 'audio/m4a', 'audio/aac',
                'application/pdf',
                'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
                'application/vnd.ms-excel', 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
                'application/vnd.ms-powerpoint', 'application/vnd.openxmlformats-officedocument.presentationml.presentation',
                'text/plain'
            ];
            return tiposPermitidos.includes(file.type) || file.name.match(/\.(jpg|jpeg|png|gif|webp|mp4|avi|mov|wmv|flv|mp3|wav|ogg|m4a|aac|pdf|doc|docx|xls|xlsx|ppt|pptx|txt)$/i);
        });

        if (archivosValidos.length !== files.length) {
            mostrarMensaje('Algunos archivos no son compatibles con WhatsApp', 'warning');
        }

        // Agregar archivos File reales a los archivos existentes (mximo 100)
        const archivosActualizados = [...archivos, ...archivosValidos].slice(0, 100);
        setArchivos(archivosActualizados);

        // Limpiar el input
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }
    };

    const eliminarArchivo = (index) => {
        const nuevosArchivos = archivos.filter((_, i) => i !== index);
        setArchivos(nuevosArchivos);
    };

    const guardarMensajeInicial = async () => {
        setGuardando(true);
        try {
            // Separar archivos nuevos (File objects) de archivos existentes (objetos con path)
            const archivosNuevos = archivos.filter(file => file instanceof File);
            const archivosExistentes = archivos.filter(file => !(file instanceof File));

            // Convertir solo los archivos nuevos a ArrayBuffer
            const archivosNuevosConArrayBuffer = await Promise.all(archivosNuevos.map(async file => {
                return new Promise((resolve, reject) => {
                    const reader = new FileReader();
                    reader.onload = () => {
                        if (reader.result instanceof ArrayBuffer) {
                            resolve({
                                name: file.name,
                                type: file.type,
                                arrayBuffer: reader.result
                            });
                        } else {
                            reject(new Error('FileReader did not return an ArrayBuffer.'));
                        }
                    };
                    reader.onerror = (error) => {
                        reject(new Error(`Error reading file ${file.name}: ${error.target.error.message}`));
                    };
                    reader.readAsArrayBuffer(file);
                });
            }));

            // Combinar archivos existentes (que ya tienen path) con archivos nuevos
            const todosLosArchivos = [...archivosExistentes, ...archivosNuevosConArrayBuffer];

            const datosParaGuardar = {
                text: mensajeTexto.trim(),
                files: todosLosArchivos
            };

            await window.electronAPI.saveMensajeInicial(datosParaGuardar, activo);
            mostrarMensaje('Mensaje inicial guardado exitosamente', 'success');
        } catch (error) {
            console.error('Error al guardar:', error);
            mostrarMensaje(error.message || 'Error al guardar el mensaje inicial', 'error');
        } finally {
            setGuardando(false);
        }
    };

    const obtenerIconoArchivo = (archivo) => {
        // Manejar tanto objetos File como objetos con propiedades {name, path, type}
        const tipo = archivo.type;
        const nombre = archivo.name;
        
        if (tipo.startsWith('image/')) return '';
        if (tipo.startsWith('video/')) return '';
        if (tipo.startsWith('audio/')) return '';
        if (tipo === 'application/pdf') return '';
        if (tipo.includes('word') || nombre.endsWith('.doc') || nombre.endsWith('.docx')) return '';
        if (tipo.includes('excel') || nombre.endsWith('.xls') || nombre.endsWith('.xlsx')) return '';
        if (tipo.includes('powerpoint') || nombre.endsWith('.ppt') || nombre.endsWith('.pptx')) return '';
        return '';
    };

    if (cargando) {
        return (
            <div className="mensaje-inicial-container">
                <div className="loading-spinner">
                    <div className="spinner"></div>
                    <p>Cargando datos...</p>
                </div>
            </div>
        );
    }

    return (
        <div className="mensaje-inicial-container">
            <div className="mensaje-inicial-header">
                <h1>Mensaje Inicial</h1>
                <div className="agente-info">
                    <span className="agente-label">Agente:</span>
                    <span className="agente-numero">{agente}</span>
                </div>
            </div>

            {mensaje.texto && (
                <div className={`mensaje-notificacion ${mensaje.tipo}`}>
                    {mensaje.texto}
                </div>
            )}

            <div className="mensaje-inicial-content">
                <div className="configuracion-section">
                    <div className="switch-container">
                        <label className="switch">
                            <input
                                type="checkbox"
                                checked={activo}
                                onChange={(e) => setActivo(e.target.checked)}
                            />
                            <span className="slider round"></span>
                        </label>
                        <span className="switch-label">
                            {activo ? 'Mensaje inicial activado' : 'Mensaje inicial desactivado'}
                        </span>
                    </div>
                </div>

                <div className="mensaje-section">
                    <label className="section-label">Texto del mensaje:</label>
                    <textarea
                        className="mensaje-textarea"
                        placeholder="Escribe aqu el mensaje inicial que se enviar automticamente..."
                        value={mensajeTexto}
                        onChange={(e) => setMensajeTexto(e.target.value)}
                        rows={6}
                    />
                </div>

                <div className="archivos-section">
                    <div className="archivos-header">
                        <label className="section-label">Archivos adjuntos:</label>
                        <button
                            className="agregar-archivos-btn"
                            onClick={() => fileInputRef.current?.click()}
                        >
                             Agregar archivos
                        </button>
                        <input
                            ref={fileInputRef}
                            type="file"
                            multiple
                            accept="image/*,video/*,audio/*,.pdf,.doc,.docx,.xls,.xlsx,.ppt,.pptx,.txt"
                            onChange={manejarSeleccionArchivos}
                            style={{ display: 'none' }}
                        />
                    </div>

                    {archivos.length > 0 && (
                        <div className="archivos-lista">
                            <div className="archivos-contador">
                                {archivos.length} archivo{archivos.length !== 1 ? 's' : ''} seleccionado{archivos.length !== 1 ? 's' : ''}
                            </div>
                            <div className="archivos-grid">
                                {archivos.map((archivo, index) => (
                                    <div key={index} className="archivo-item">
                                        <div className="archivo-info">
                                            <span className="archivo-icono">
                                                {obtenerIconoArchivo(archivo)}
                                            </span>
                                            <span className="archivo-nombre" title={archivo.name}>
                                                {archivo.name}
                                            </span>
                                        </div>
                                        <button
                                            className="eliminar-archivo-btn"
                                            onClick={() => eliminarArchivo(index)}
                                            title="Eliminar archivo"
                                        >
                                            
                                        </button>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}
                </div>

                <div className="acciones-section">
                    <button
                        className="guardar-btn"
                        onClick={guardarMensajeInicial}
                        disabled={guardando}
                    >
                        {guardando ? (
                            <>
                                <span className="btn-spinner"></span>
                                Guardando...
                            </>
                        ) : (
                            ' Guardar mensaje inicial'
                        )}
                    </button>
                </div>

                <div className="info-section">
                    <div className="info-box">
                        <h3> Informacin</h3>
                        <ul>
                            <li>El mensaje inicial se enviar automticamente cuando est activado</li>
                            <li>Puedes adjuntar archivos compatibles con WhatsApp</li>
                            <li>Los tipos de archivo soportados incluyen: imgenes, videos, audios, PDFs y documentos de Office</li>
                            <li>Puedes agregar mltiples archivos al mensaje</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default MensajeInicial;