// src/components/Condiciones.jsx
import React, { useState, useEffect } from 'react';
import './Condiciones.css';

const Condiciones = ({ onConditionsAccepted }) => {
  const [showConditionsModal, setShowConditionsModal] = useState(false);
  const [isAccepting, setIsAccepting] = useState(false);
  
  // Estados para los campos de cuenta
  const [correo, setCorreo] = useState('');
  const [clave, setClave] = useState('');
  
  // Estado para las condiciones
  const [conditionsChecked, setConditionsChecked] = useState(true);

  // Estados para manejo de errores (mejor que alert)
  const [errorMessage, setErrorMessage] = useState('');
  const [showError, setShowError] = useState(false);

  // Debug: Monitorear cambios en los estados
  useEffect(() => {
    console.log(' Estado correo:', correo);
  }, [correo]);

  useEffect(() => {
    console.log(' Estado clave:', clave);
  }, [clave]);

  useEffect(() => {
    console.log(' Estado isAccepting:', isAccepting);
  }, [isAccepting]);

  // Efecto principal para verificar condiciones al montar
  useEffect(() => {
    checkConditionsStatus();
  }, []);

// Funcin asncrona para generar una clave aleatoria de 128 caracteres alfanumricos
    async function RandomKey(length = 128) {
      // Caracteres permitidos: letras maysculas, minsculas y nmeros
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      
      // Simular operacin asncrona para no bloquear el hilo principal
      await new Promise(resolve => setTimeout(resolve, 0));
      
      // Generar cada carcter aleatoriamente
      for (let i = 0; i < length; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        result += characters[randomIndex];
      }
      
      return result;
    }

  // Funcin para verificar el estado de las condiciones
  const checkConditionsStatus = async () => {
    try {
      if (!window.electronAPI) {
        console.error(' electronAPI no disponible');
        onConditionsAccepted?.(true);
        return;
      }

      console.log(' Verificando estado de condiciones...');
      const conditionsAccepted = await window.electronAPI.getCondiciones();
      console.log(' Resultado getCondiciones:', conditionsAccepted);
      
      if (!conditionsAccepted) {
        console.log(' Condiciones no aceptadas, mostrando modal');
        setShowConditionsModal(true);
      } else {
        console.log(' Condiciones ya aceptadas - notificando al padre');
        onConditionsAccepted?.();
      }
      
    } catch (error) {
      console.error(' Error al verificar condiciones:', error);
      console.log(' Error - permitiendo continuar');
      onConditionsAccepted?.();
    }
  };

  // Funcin para aceptar condiciones
  const acceptConditions = async () => {
    console.log(' Iniciando proceso de aceptacin de condiciones...');
    console.log(' Correo:', correo);
    console.log(' Clave length:', clave.length);
    
    setIsAccepting(true);
    setShowError(false); // Limpiar errores anteriores

    try {
      if (!window.electronAPI) {
        throw new Error('ElectronAPI no disponible');
      }

      const randomKey = await RandomKey();  
      
      const correoMinusculas = correo.toLocaleLowerCase();

      const cuentaGuardada = await window.electronAPI.saveCuenta(correoMinusculas, clave, randomKey);
      console.log(' Resultado saveCuenta:', cuentaGuardada);
      
      if (cuentaGuardada) {
        await window.electronAPI.saveCondiciones(true);
        console.log(' Condiciones guardadas exitosamente');
        setShowConditionsModal(false);
        console.log(' Modal cerrado - notificando aceptacin al padre');
        onConditionsAccepted?.();
      } else {
        console.log(' Error: cuenta no guardada');
        setErrorMessage('Error al validar la cuenta. Verifica tus datos.');
        setShowError(true);
      }
      
    } catch (error) {
      console.error(' Error al guardar condiciones:', error);
      setErrorMessage('Error al guardar la aceptacin. Intntalo de nuevo.');
      setShowError(true);
    } finally {
      setIsAccepting(false);
    }
  };

  // Funcin para abrir condiciones en navegador externo
  const openConditionsInBrowser = () => {
    if (window.electronAPI?.openExternalLink) {
      window.electronAPI.openExternalLink('https://login.artechclick.com/');
    }
  };

  if (!showConditionsModal) {
    return null;
  }

    // Abrir tutorial externo
  const openTutorial = () => {
    if (window.electronAPI?.openExternalLink) {
      window.electronAPI.openExternalLink('https://www.youtube.com/watch?v=txcYee5jZV8');
    }
  };

  return (
    <div className="modal-backdrop show" style={{ zIndex: 3000 }}>
      <div className="modal-content conditions-modal-optimized">
        
        {/* Header compacto */}
        <div className="conditions-header">
          <div className="header-icon"></div>
          <div className="header-text">
            <h2>Registro en WAI</h2>
            <p>Ingresa tus credenciales para usar Agente IA WhatsApp</p>
          </div>
        </div>

        {/* Contenido principal scrolleable */}
        <div className="conditions-body">
          
          {/* Campos de cuenta */}
          <div className="account-section">
            <div className="input-group">
              <div className="input-row">
                <input 
                  type="email" 
                  value={correo}
                  onChange={(e) => {
                    console.log(' Cambio en correo:', e.target.value);
                    setCorreo(e.target.value);
                    // Limpiar error al escribir
                    if (showError) setShowError(false);
                  }}
                  placeholder=" Correo electrnico"
                  className="form-input"
                  disabled={isAccepting}
                  autoComplete="off"
                />
              </div>
              <div className="input-row">
                <input 
                  type="text" 
                  value={clave}
                  onChange={(e) => {
                    console.log(' Cambio en clave, length:', e.target.value.length);
                    setClave(e.target.value);
                    // Limpiar error al escribir
                    if (showError) setShowError(false);
                  }}
                  placeholder=" Clave Licencia"
                  className="form-input"
                  disabled={isAccepting}
                  autoComplete="off"
                />
              </div>
            </div>
          </div>

          {/* Video tutorial compacto */}
          <div className="tutorial-section">
            <div className="tutorial-header">
              
              <button type="button" className="btn-tutorial-main" onClick={openTutorial} disabled={isAccepting}>
              <span className="btn-icon" aria-hidden>
                <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round">
                  <circle cx="12" cy="12" r="10" />
                  <polygon points="10 8 16 12 10 16 10 8" />
                </svg>
              </span>
              Tutorial: Cuenta gratuita de WAI
            </button>

            </div>
            <div className="video-container">

              <img
                src="https://img.youtube.com/vi/TsBLBudEMXE/maxresdefault.jpg"
                alt="Tutorial WAI"
                onClick={openTutorial} // Reutiliza la funcin que ya existe
                className="tutorial-video"
                style={{
                  width: '100%',
                  height: '200px', // Mantiene el tamao del iframe
                  borderRadius: '5px',
                  cursor: 'pointer', // Muestra que es clickeable
                  objectFit: 'cover' // Asegura que la imagen cubra el espacio
                }}
              />
            </div>
          </div>

          {/* Trminos y condiciones */}
          <div className="terms-section">
            <div className="terms-checkbox">
              <input 
                type="checkbox" 
                id="accept-terms"
                checked={conditionsChecked}
                readOnly
                className="checkbox-input"
              />
              <label htmlFor="accept-terms" className="checkbox-label">
                Acepto los{' '}
                <a 
                  href="https://artechclick.com/condiciones/" 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="terms-link"
                >
                  trminos y condiciones
                </a>
              </label>
            </div>
          </div>

        </div>

        {/* Footer con botones */}
        <div className="conditions-footer">
          <button 
            className="btn-secondary"
            onClick={openConditionsInBrowser}
            title="Regstrate en WAI"
          >
             Registrarse
          </button>
          
          <button 
            className="btn-primary"
            onClick={acceptConditions}
            disabled={isAccepting || !correo || !clave || !conditionsChecked}
          >
            {isAccepting ? (
              <>
                <span className="spinner-mini"></span>
                Validando...
              </>
            ) : (
              <>
                <span></span>
                Ingresar
              </>
            )}
          </button>
        </div>

        {/* Nota de advertencia o error */}
        <div className="warning-note">
          {showError ? (
            <>
              <span className="error-icon"></span>
              <span className="error-text">{errorMessage}</span>
              <button 
                className="error-close"
                onClick={() => setShowError(false)}
                aria-label="Cerrar error"
              >
                
              </button>
            </>
          ) : (
            <>
              <span className="warning-icon"></span>
              <span className="warning-text">Debes completar el registro para continuar</span>
            </>
          )}
        </div>

      </div>
    </div>
  );
};

export default Condiciones;