import React, { useState, useEffect } from 'react';

const Agentes = ({ alCambiarAgente }) => {
  const [agenteActivo, setAgenteActivo] = useState("1");

  // Recuperar valor de agente al montar el componente
  useEffect(() => {
    const cargarAgente = async () => {
      const valorAgente = await window.electronAPI.getAgente();
      setAgenteActivo(String(valorAgente));
    };
    cargarAgente();
  }, []);

  const manejarClickAgente = async (numeroAgente) => {
    await window.electronAPI.agenteValor(numeroAgente);
    setAgenteActivo(numeroAgente);
    
    // Notificar a App.jsx para que se re-monte
    if (alCambiarAgente) {
      alCambiarAgente(numeroAgente);
    }
  };

  const agentes = ["1", "2", "3", "4", "5"];

  return (
    <div style={{
      position: 'fixed',
      right: '20px',
      top: '50%',
      transform: 'translateY(-50%)',
      zIndex: 1000,
      display: 'flex',
      flexDirection: 'column',
      gap: '8px',
      background: 'var(--color-bg-light, #f8f9fa)',
      padding: '12px',
      borderRadius: '12px',
      boxShadow: '0 4px 20px rgba(var(--color-text-dark-rgb, 26, 32, 44), 0.15)',
      border: '1px solid var(--color-accent2, #c5dcf0)'
    }}>
      <div style={{
        fontSize: '12px',
        fontWeight: '600',
        color: 'var(--color-text-gray, #4a5568)',
        textAlign: 'center',
        marginBottom: '4px',
        letterSpacing: '0.5px'
      }}>
        AGENTES
      </div>
      
      {agentes.map((numero) => (
        <button
          key={numero}
          onClick={() => manejarClickAgente(numero)}
          style={{
            width: '45px',
            height: '45px',
            borderRadius: '8px',
            border: 'none',
            fontSize: '14px',
            fontWeight: '600',
            cursor: 'pointer',
            transition: 'all 0.2s ease',
            background: agenteActivo === numero 
              ? 'var(--color-primary, #000191)' 
              : 'var(--color-accent1, #adc9e7)',
            color: agenteActivo === numero 
              ? 'white' 
              : 'var(--color-text-dark, #1a202c)',
            boxShadow: agenteActivo === numero
              ? '0 2px 8px rgba(var(--color-primary-rgb, 0, 1, 145), 0.3)'
              : '0 2px 4px rgba(var(--color-text-dark-rgb, 26, 32, 44), 0.1)',
            position: 'relative',
            overflow: 'hidden'
          }}
          onMouseEnter={(e) => {
            if (agenteActivo !== numero) {
              e.target.style.background = 'var(--color-secondary, #0eda91)';
              e.target.style.color = 'white';
              e.target.style.transform = 'scale(1.05)';
              e.target.style.boxShadow = '0 4px 12px rgba(var(--color-secondary-rgb, 14, 218, 145), 0.4)';
            }
          }}
          onMouseLeave={(e) => {
            if (agenteActivo !== numero) {
              e.target.style.background = 'var(--color-accent1, #adc9e7)';
              e.target.style.color = 'var(--color-text-dark, #1a202c)';
              e.target.style.transform = 'scale(1)';
              e.target.style.boxShadow = '0 2px 4px rgba(var(--color-text-dark-rgb, 26, 32, 44), 0.1)';
            }
          }}
          onMouseDown={(e) => {
            e.target.style.transform = 'scale(0.95)';
          }}
          onMouseUp={(e) => {
            e.target.style.transform = agenteActivo === numero ? 'scale(1)' : 'scale(1.05)';
          }}
        >
          {numero}
          
          {/* Indicador de activo */}
          {agenteActivo === numero && (
            <div style={{
              position: 'absolute',
              top: '3px',
              right: '3px',
              width: '8px',
              height: '8px',
              borderRadius: '50%',
              background: 'var(--color-secondary, #0eda91)',
              boxShadow: '0 0 4px rgba(var(--color-secondary-rgb, 14, 218, 145), 0.6)'
            }} />
          )}
        </button>
      ))}
    </div>
  );
};

export default Agentes;