import React from 'react';
import { DollarSign, Link, Users, Play } from 'lucide-react';

const Reventa = () => {
  // Estado para manejar la animacin del video y el botn CTA
  const [hoveredButton, setHoveredButton] = React.useState(false);
  const [hoveredVideo, setHoveredVideo] = React.useState(false);

  // Datos del video
  const videoId = "ekwrVefIUfQ";
  const videoThumbnail = `https://img.youtube.com/vi/${videoId}/maxresdefault.jpg`;
  const videoLink = `https://www.youtube.com/watch?v=${videoId}`;

  const styles = {
    container: {
      paddingTop: '200px',
      minHeight: '100vh',
      backgroundColor: '#f8f9fa',
      fontFamily: "'Inter', sans-serif",
    },
    wrapper: {
      maxWidth: '1000px',
      margin: '0 auto',
      padding: '0 20px',
    },
    header: {
      textAlign: 'center',
      marginBottom: '60px',
    },
    title: {
      fontSize: '42px',
      fontWeight: '800',
      color: '#000191',
      marginBottom: '16px',
    },
    highlight: {
      color: '#0eda91',
    },
    subtitle: {
      fontSize: '18px',
      color: '#4a5568',
      lineHeight: '1.6',
      maxWidth: '700px',
      margin: '0 auto',
    },
    mainCard: {
      background: 'white',
      borderRadius: '24px',
      padding: '40px',
      boxShadow: '0 10px 40px rgba(0, 1, 145, 0.08)',
      marginBottom: '40px',
    },
    commission: {
      background: 'linear-gradient(135deg, #0eda91 0%, #adc9e7 100%)',
      borderRadius: '16px',
      padding: '30px',
      textAlign: 'center',
      marginBottom: '40px',
    },
    commissionNumber: {
      fontSize: '56px',
      fontWeight: '800',
      color: '#000191',
      marginBottom: '8px',
    },
    commissionText: {
      fontSize: '20px',
      color: '#1a202c',
      fontWeight: '600',
    },
    steps: {
      display: 'grid',
      gap: '24px',
      marginBottom: '40px',
    },
    step: {
      display: 'flex',
      alignItems: 'flex-start',
      gap: '16px',
    },
    stepIcon: {
      width: '40px',
      height: '40px',
      background: '#e7effc',
      borderRadius: '12px',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      flexShrink: '0',
      color: '#000191',
    },
    stepContent: {
      flex: '1',
    },
    stepTitle: {
      fontSize: '18px',
      fontWeight: '600',
      color: '#1a202c',
      marginBottom: '4px',
    },
    stepText: {
      color: '#4a5568',
      fontSize: '15px',
      lineHeight: '1.5',
    },
    
    // --- ESTILOS DEL VIDEO PLAYER (Facade) ---
    videoSection: {
      marginBottom: '40px',
    },
    videoTitle: {
      fontSize: '20px',
      fontWeight: '600',
      color: '#1a202c',
      marginBottom: '20px',
      textAlign: 'center',
    },
    videoCard: {
      position: 'relative',
      paddingBottom: '56.25%', // Aspect ratio 16:9
      height: 0,
      overflow: 'hidden',
      borderRadius: '20px',
      cursor: 'pointer',
      boxShadow: hoveredVideo 
        ? '0 20px 40px rgba(0, 1, 145, 0.2)' 
        : '0 10px 20px rgba(0, 1, 145, 0.1)',
      transition: 'box-shadow 0.3s ease',
      backgroundColor: '#000',
    },
    thumbnail: {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      objectFit: 'cover',
      opacity: hoveredVideo ? 0.9 : 1,
      transform: hoveredVideo ? 'scale(1.05)' : 'scale(1)',
      transition: 'transform 0.5s ease, opacity 0.3s ease',
    },
    playButtonWrapper: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: '80px',
      height: '80px',
      backgroundColor: 'rgba(0, 1, 145, 0.6)',
      backdropFilter: 'blur(8px)',
      borderRadius: '50%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      border: '2px solid rgba(255, 255, 255, 0.8)',
      boxShadow: '0 4px 20px rgba(0,0,0,0.4)',
      zIndex: 2,
      transition: 'transform 0.3s ease, background-color 0.3s ease',
      ...(hoveredVideo ? { transform: 'translate(-50%, -50%) scale(1.1)', backgroundColor: 'rgba(0, 1, 145, 0.8)' } : {})
    },
    playIcon: {
      color: '#fff',
      fill: '#fff',
      marginLeft: '5px',
    },
    videoLabel: {
      position: 'absolute',
      bottom: '20px',
      width: '100%',
      textAlign: 'center',
      color: 'white',
      zIndex: 2,
      fontWeight: '600',
      textShadow: '0 2px 4px rgba(0,0,0,0.7)',
      opacity: hoveredVideo ? 1 : 0.8,
      transition: 'opacity 0.3s ease',
    },
    // -----------------------------------------

    ctaButton: {
      background: 'linear-gradient(135deg, #000191 0%, #0eda91 100%)',
      color: 'white',
      padding: '16px 32px',
      borderRadius: '50px',
      border: 'none',
      fontSize: '16px',
      fontWeight: '600',
      cursor: 'pointer',
      transition: 'transform 0.3s ease',
      display: 'block',
      margin: '0 auto',
      boxShadow: '0 5px 20px rgba(0, 1, 145, 0.2)',
    },
    ctaButtonHover: {
      transform: 'translateY(-2px)',
    },
    link: {
      color: '#0eda91',
      textDecoration: 'none',
      fontWeight: '600',
    },
  };

  return (
    <div style={styles.container}>
      <div style={styles.wrapper}>
        {/* Header */}
        <div style={styles.header}>
          <h1 style={styles.title}>
            Programa de <span style={styles.highlight}>Reventa WAI</span>
          </h1>
          <p style={styles.subtitle}>
            Accede al programa adquiriendo una licencia <strong>Mensual, Anual o Vitalicia</strong>. 
            Empieza a generar ingresos pasivos hoy mismo!
          </p>
        </div>

        {/* Main Card */}
        <div style={styles.mainCard}>
          {/* Commission */}
          <div style={styles.commission}>
            <div style={styles.commissionNumber}>50%</div>
            <div style={styles.commissionText}>de Comisin por Venta</div>
          </div>

          {/* Steps - Actualizados con el nuevo flujo */}
          <div style={styles.steps}>
            {/* Paso 1: Enlace Inteligente */}
            <div style={styles.step}>
              <div style={styles.stepIcon}>
                <Link size={24} />
              </div>
              <div style={styles.stepContent}>
                <h3 style={styles.stepTitle}>Copia tu Enlace Inteligente</h3>
                <p style={styles.stepText}>
                  Ya no necesitas comprar para revender manualmente. Simplemente copia tu 
                  enlace nico de referido desde el{' '}
                  <a href="#" 
                     style={styles.link} 
                     onClick={(e) => {
                       e.preventDefault();
                       window.electronAPI.openExternalLink('https://wai.artechclick.com/panel');
                     }}>
                    Panel de Control
                  </a>.
                </p>
              </div>
            </div>

            {/* Paso 2: Rastreo Automtico */}
            <div style={styles.step}>
              <div style={styles.stepIcon}>
                <Users size={24} />
              </div>
              <div style={styles.stepContent}>
                <h3 style={styles.stepTitle}>Comparte y Promociona</h3>
                <p style={styles.stepText}>
                  Comparte el enlace con tus clientes. Cuando hacen clic, nuestro sistema los 
                  rastrea y, al registrarse, quedan vinculados automticamente como tus referidos.
                </p>
              </div>
            </div>

            {/* Paso 3: Ganancia */}
            <div style={styles.step}>
              <div style={styles.stepIcon}>
                <DollarSign size={24} />
              </div>
              <div style={styles.stepContent}>
                <h3 style={styles.stepTitle}>Gana el 50% Automtico</h3>
                <p style={styles.stepText}>
                  Cuando tu referido compra una licencia (ya sea mensual o anual), 
                  se te abona automticamente el 50% del valor de esa compra a tus ganancias.
                </p>
              </div>
            </div>
          </div>

          {/* Video Section Renovada */}
          <div style={styles.videoSection}>
            <h2 style={styles.videoTitle}>Mira cmo funciona el nuevo sistema</h2>
            
            <div 
              style={styles.videoCard}
              onMouseEnter={() => setHoveredVideo(true)}
              onMouseLeave={() => setHoveredVideo(false)}
              onClick={() => window.electronAPI.openExternalLink(videoLink)}
              title="Ver tutorial en YouTube"
            >
              {/* Miniatura */}
              <img 
                src={videoThumbnail} 
                alt="Tutorial WAI" 
                style={styles.thumbnail}
              />
              
              {/* Botn Play Overlay */}
              <div style={styles.playButtonWrapper}>
                <Play size={32} style={styles.playIcon} />
              </div>

              {/* Texto opcional inferior */}
              <div style={styles.videoLabel}>
                Clic para ver video tutorial
              </div>
            </div>
          </div>

          {/* CTA Button */}
          <button 
            style={{
              ...styles.ctaButton,
              ...(hoveredButton ? styles.ctaButtonHover : {})
            }}
            onMouseEnter={() => setHoveredButton(true)}
            onMouseLeave={() => setHoveredButton(false)}
            onClick={() => window.electronAPI.openExternalLink('https://wai.artechclick.com')}
          >
            Obtener mi Enlace de Referido
          </button>
        </div>
      </div>
    </div>
  );
};

export default Reventa;