import React, { useState, useEffect } from 'react';
import './Clientes.css';

const Clientes = () => {
  const [clientes, setClientes] = useState({});
  const [agenteActual, setAgenteActual] = useState('1');
  const [loading, setLoading] = useState(true);
  const [clienteSeleccionado, setClienteSeleccionado] = useState(null);
  const [busqueda, setBusqueda] = useState('');
  const [filtroEstado, setFiltroEstado] = useState('todos'); // todos, activos, inactivos
  const [mostrarFormularioNuevo, setMostrarFormularioNuevo] = useState(false);
  const [mostrarEmojis, setMostrarEmojis] = useState(false);
  const [formData, setFormData] = useState({
    nombre: '',
    etiqueta: '',
    color: '#adc9e7',
    activo: true
  });
  const [nuevoCliente, setNuevoCliente] = useState({
    numero: '',
    nombre: '',
    etiqueta: '',
    color: '#adc9e7',
    activo: true
  });

  // Variables'
  const [notification, setNotification] = useState({ visible: false, message: '', type: '' });

  // Colores predefinidos
  const coloresPredefinidos = [
    '#adc9e7', '#c5dcf0', '#0eda91', '#000191',
    '#ff6b6b', '#4ecdc4', '#45b7d1', '#f9ca24',
    '#f0932b', '#eb4d4b', '#6c5ce7', '#a29bfe'
  ];

  // Etiquetas predefinidas
  const etiquetasPredefinidas = [
    'Contactado ',
    'Cliente VIP ',
    'Nuevo Cliente ',
    'Cliente Frecuente ',
    'En Seguimiento ',
    'Oportunidad ',
    'Inactivo ',
    'Prioritario '
  ];

  // Emojis disponibles
  const emojisDisponibles = [
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', ''
  ];

  useEffect(() => {
    cargarDatos();
  }, []);

  const cargarDatos = async () => {
    try {
      setLoading(true);
      
      // Obtener agente actual
      const agente = await window.electronAPI.getAgente();
      setAgenteActual(agente);

      // Obtener clientes usando getTotalClientes
      const clientesData = await window.electronAPI.getTotalClientes();
      setClientes(clientesData || {});
      
    } catch (error) {
      console.error('Error al cargar datos:', error);
    } finally {
      setLoading(false);
    }
  };

  // Filtrar y buscar clientes
  const clientesFiltrados = Object.entries(clientes).filter(([numero, cliente]) => {
    const cumpleBusqueda = busqueda === '' || 
                          numero.includes(busqueda) || 
                          (cliente.nombre && cliente.nombre.toLowerCase().includes(busqueda.toLowerCase()));
    
    const cumpleFiltro = filtroEstado === 'todos' || 
                        (filtroEstado === 'activos' && cliente.activo) ||
                        (filtroEstado === 'inactivos' && !cliente.activo);
    
    return cumpleBusqueda && cumpleFiltro;
  });

  const seleccionarCliente = (numero) => {
    const cliente = clientes[numero];
    setClienteSeleccionado(numero);
    setFormData({
      nombre: cliente.nombre || '',
      etiqueta: cliente.etiqueta || '',
      color: cliente.color || '#adc9e7',
      activo: cliente.activo !== false
    });
    setMostrarFormularioNuevo(false);
  };

    // Funcin para mostrar notificaciones
  const showNotification = (message, type = 'success') => {
      setNotification({ visible: true, message, type });
  };

  // Hook para ocultar la notificacin automticamente
  useEffect(() => {
      if (notification.visible) {
          const timer = setTimeout(() => {
              setNotification(prev => ({ ...prev, visible: false }));
          }, 3000); // La notificacin desaparecer despus de 3 segundos

          return () => clearTimeout(timer); // Limpia el temporizador si el componente se actualiza
      }
  }, [notification.visible]);

  const guardarCambios = async () => {
    if (!clienteSeleccionado) return;

    // Valores por defecto si estn vacos
    const nombreFinal = formData.nombre.trim() || 'cliente';
    const etiquetaFinal = formData.etiqueta.trim() || 'nuevo';

    try {
      await window.electronAPI.saveClientes(
        clienteSeleccionado,
        agenteActual,
        formData.activo,
        nombreFinal,
        etiquetaFinal,
        formData.color
      );

      // Actualizar estado local inmediatamente
      setClientes(prev => ({
        ...prev,
        [clienteSeleccionado]: {
          ...prev[clienteSeleccionado],
          nombre: nombreFinal,
          etiqueta: etiquetaFinal,
          color: formData.color,
          activo: formData.activo
        }
      }));

      // Actualizar el formulario con los valores finales
      setFormData(prev => ({
        ...prev,
        nombre: nombreFinal,
        etiqueta: etiquetaFinal
      }));

      showNotification(' Cliente actualizado exitosamente');
    } catch (error) {
      console.error('Error al guardar:', error);
      showNotification(' Error al guardar los cambios');
    }
  };

  const agregarCliente = async () => {
    if (!nuevoCliente.numero.trim()) {
      showNotification(' El nmero de telfono es obligatorio');
      return;
    }

    if (clientes[nuevoCliente.numero]) {
      showNotification(' Ya existe un cliente con este nmero');
      return;
    }

    // Valores por defecto si estn vacos
    const nombreFinal = nuevoCliente.nombre.trim() || 'cliente';
    const etiquetaFinal = nuevoCliente.etiqueta.trim() || 'nuevo';

    try {
      await window.electronAPI.saveClientes(
        nuevoCliente.numero,
        agenteActual,
        nuevoCliente.activo,
        nombreFinal,
        etiquetaFinal,
        nuevoCliente.color
      );

      // Actualizar estado local inmediatamente
      setClientes(prev => ({
        ...prev,
        [nuevoCliente.numero]: {
          nombre: nombreFinal,
          etiqueta: etiquetaFinal,
          color: nuevoCliente.color,
          activo: nuevoCliente.activo
        }
      }));

      // Limpiar formulario
      setNuevoCliente({
        numero: '',
        nombre: '',
        etiqueta: '',
        color: '#adc9e7',
        activo: true
      });

      setMostrarFormularioNuevo(false);
      showNotification(' Cliente agregado exitosamente');

    } catch (error) {
      console.error('Error al agregar cliente:', error);
      showNotification(' Error al agregar el cliente');
    }
  };

  const toggleActivo = async (numero) => {
    const cliente = clientes[numero];
    if (!cliente) return;
    
    const nuevoEstado = !cliente.activo;

    try {
      await window.electronAPI.saveClientes(
        numero,
        agenteActual,
        nuevoEstado,
        cliente.nombre || 'cliente',
        cliente.etiqueta || 'nuevo',
        cliente.color || '#adc9e7'
      );

      // Actualizar estado local inmediatamente
      setClientes(prev => ({
        ...prev,
        [numero]: {
          ...prev[numero],
          activo: nuevoEstado
        }
      }));

      // Si es el cliente seleccionado, actualizar el formulario
      if (clienteSeleccionado === numero) {
        setFormData(prev => ({ ...prev, activo: nuevoEstado }));
      }

    } catch (error) {
      console.error('Error al cambiar estado:', error);
      showNotification(' Error al cambiar el estado del cliente');
    }
  };

  const agregarEmojiAEtiqueta = (emoji, esNuevoCliente = false) => {
    if (esNuevoCliente) {
      setNuevoCliente(prev => ({
        ...prev,
        etiqueta: (prev.etiqueta || '') + emoji
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        etiqueta: (prev.etiqueta || '') + emoji
      }));
    }
    setMostrarEmojis(false);
  };

  if (loading) {
    return (
      <div className="clientes-container">
        <div className="loading">Cargando clientes...</div>
      </div>
    );
  }

  return (
    <div className="clientes-container">
      <header className="clientes-header">
        <h1>Gestin de Clientes</h1>
        <div className="agente-info">
          <span className="agente-badge">Agente {agenteActual}</span>
          <span className="clientes-count">{Object.keys(clientes).length} clientes</span>
        </div>
      </header>
      {notification.visible && (
          <div className={`notification ${notification.type}`}>
              {notification.message}
          </div>
      )}

      <div className="controles-superiores">
        <div className="busqueda-filtros">
          <input
            type="text"
            placeholder=" Buscar por nombre o nmero..."
            value={busqueda}
            onChange={(e) => setBusqueda(e.target.value)}
            className="input-busqueda"
          />
          
          <select
            value={filtroEstado}
            onChange={(e) => setFiltroEstado(e.target.value)}
            className="filtro-estado"
          >
            <option value="todos">Todos los clientes</option>
            <option value="activos">Solo activos</option>
            <option value="inactivos">Solo inactivos</option>
          </select>
        </div>

        <button 
          className="btn-agregar-cliente"
          onClick={() => {
            setMostrarFormularioNuevo(!mostrarFormularioNuevo);
            setClienteSeleccionado(null);
          }}
        >
          + Agregar Cliente
        </button>
      </div>

      <div className="clientes-content">
        {/* Lista de clientes */}
        <div className="clientes-lista">
          <h2>Lista de Clientes</h2>
          
          {clientesFiltrados.length === 0 ? (
            <div className="no-clientes">
              <p>{busqueda || filtroEstado !== 'todos' ? 'No se encontraron clientes con los filtros aplicados' : 'No hay clientes registrados para este agente'}</p>
            </div>
          ) : (
            <div className="lista-items">
              {clientesFiltrados.map(([numero, cliente]) => (
                <div 
                  key={numero}
                  className={`cliente-item ${clienteSeleccionado === numero ? 'selected' : ''} ${!cliente.activo ? 'inactivo' : ''}`}
                  onClick={() => seleccionarCliente(numero)}
                >
                  <div className="cliente-info">
                    <div className="cliente-header">
                      <span className="cliente-numero">{numero}</span>
                      <div className="cliente-estado">
                        <button
                          className={`toggle-activo ${cliente.activo ? 'activo' : 'inactivo'}`}
                          onClick={(e) => {
                            e.stopPropagation();
                            toggleActivo(numero);
                          }}
                          title={cliente.activo ? 'Desactivar cliente' : 'Activar cliente'}
                        >
                          {cliente.activo ? '' : ''}
                        </button>
                      </div>
                    </div>
                    <div className="cliente-detalles">
                      <span className="cliente-nombre">{cliente.nombre || 'cliente'}</span>
                      <span 
                        className="cliente-etiqueta"
                        style={{ backgroundColor: cliente.color || '#adc9e7' }}
                      >
                        {cliente.etiqueta || 'nuevo'}
                      </span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Panel de edicin o nuevo cliente */}
        <div className="clientes-editor">
          <h2>{mostrarFormularioNuevo ? 'Agregar Nuevo Cliente' : 'Editar Cliente'}</h2>
          
          {mostrarFormularioNuevo ? (
            <div className="editor-form">
              <div className="form-group">
                <label>Nmero de Telfono *</label>
                <input
                  type="text"
                  value={nuevoCliente.numero}
                  onChange={(e) => setNuevoCliente(prev => ({ ...prev, numero: e.target.value }))}
                  placeholder="Ej: 573214567890"
                />
              </div>

              <div className="form-group">
                <label>Nombre del Cliente</label>
                <input
                  type="text"
                  value={nuevoCliente.nombre}
                  onChange={(e) => setNuevoCliente(prev => ({ ...prev, nombre: e.target.value }))}
                  placeholder="Nombre del cliente (por defecto: cliente)"
                />
              </div>

              <div className="form-group">
                <label>Etiqueta</label>
                <div className="etiqueta-input-group">
                  <input
                    type="text"
                    value={nuevoCliente.etiqueta}
                    onChange={(e) => setNuevoCliente(prev => ({ ...prev, etiqueta: e.target.value }))}
                    placeholder="Etiqueta personalizada (por defecto: nuevo)"
                  />
                  <button 
                    type="button"
                    className="btn-emoji"
                    onClick={() => setMostrarEmojis(!mostrarEmojis)}
                  >
                    
                  </button>
                </div>
                
                <div className="etiquetas-predefinidas">
                  {etiquetasPredefinidas.map(etiqueta => (
                    <button
                      key={etiqueta}
                      type="button"
                      className="etiqueta-predefinida"
                      onClick={() => setNuevoCliente(prev => ({ ...prev, etiqueta: etiqueta }))}
                    >
                      {etiqueta}
                    </button>
                  ))}
                </div>
                
                {mostrarEmojis && (
                  <div className="emoji-picker">
                    {emojisDisponibles.map(emoji => (
                      <button
                        key={emoji}
                        className="emoji-option"
                        onClick={() => agregarEmojiAEtiqueta(emoji, true)}
                      >
                        {emoji}
                      </button>
                    ))}
                  </div>
                )}
              </div>

              <div className="form-group">
                <label>Color de Etiqueta</label>
                <div className="color-selector">
                  {coloresPredefinidos.map(color => (
                    <button
                      key={color}
                      className={`color-option ${nuevoCliente.color === color ? 'selected' : ''}`}
                      style={{ backgroundColor: color }}
                      onClick={() => setNuevoCliente(prev => ({ ...prev, color }))}
                      title={color}
                    />
                  ))}
                </div>
                <input
                  type="color"
                  value={nuevoCliente.color}
                  onChange={(e) => setNuevoCliente(prev => ({ ...prev, color: e.target.value }))}
                  className="color-input"
                />
              </div>

              <div className="form-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={nuevoCliente.activo}
                    onChange={(e) => setNuevoCliente(prev => ({ ...prev, activo: e.target.checked }))}
                  />
                  Cliente Activo
                </label>
              </div>

              <div className="preview-etiqueta">
                <span>Vista previa: </span>
                <span 
                  className="etiqueta-preview"
                  style={{ backgroundColor: nuevoCliente.color }}
                >
                  {nuevoCliente.etiqueta}
                </span>
              </div>

              <div className="botones-formulario">
                <button className="btn-guardar" onClick={agregarCliente}>
                   Agregar Cliente
                </button>
                <button 
                  className="btn-cancelar" 
                  onClick={() => setMostrarFormularioNuevo(false)}
                >
                  Cancelar
                </button>
              </div>
            </div>
          ) : clienteSeleccionado ? (
            <div className="editor-form">
              <div className="cliente-seleccionado">
                <strong>Cliente: {clienteSeleccionado}</strong>
              </div>

              <div className="form-group">
                <label>Nombre del Cliente</label>
                <input
                  type="text"
                  value={formData.nombre}
                  onChange={(e) => setFormData(prev => ({ ...prev, nombre: e.target.value }))}
                  placeholder="Nombre del cliente (por defecto: cliente)"
                />
              </div>

              <div className="form-group">
                <label>Etiqueta</label>
                <div className="etiqueta-input-group">
                  <input
                    type="text"
                    value={formData.etiqueta}
                    onChange={(e) => setFormData(prev => ({ ...prev, etiqueta: e.target.value }))}
                    placeholder="Etiqueta personalizada (por defecto: nuevo)"
                  />
                  <button 
                    type="button"
                    className="btn-emoji"
                    onClick={() => setMostrarEmojis(!mostrarEmojis)}
                  >
                    
                  </button>
                </div>
                
                <div className="etiquetas-predefinidas">
                  {etiquetasPredefinidas.map(etiqueta => (
                    <button
                      key={etiqueta}
                      type="button"
                      className="etiqueta-predefinida"
                      onClick={() => setFormData(prev => ({ ...prev, etiqueta: etiqueta }))}
                    >
                      {etiqueta}
                    </button>
                  ))}
                </div>
                
                {mostrarEmojis && (
                  <div className="emoji-picker">
                    {emojisDisponibles.map(emoji => (
                      <button
                        key={emoji}
                        className="emoji-option"
                        onClick={() => agregarEmojiAEtiqueta(emoji, false)}
                      >
                        {emoji}
                      </button>
                    ))}
                  </div>
                )}
              </div>

              <div className="form-group">
                <label>Color de Etiqueta</label>
                <div className="color-selector">
                  {coloresPredefinidos.map(color => (
                    <button
                      key={color}
                      className={`color-option ${formData.color === color ? 'selected' : ''}`}
                      style={{ backgroundColor: color }}
                      onClick={() => setFormData(prev => ({ ...prev, color }))}
                      title={color}
                    />
                  ))}
                </div>
                <input
                  type="color"
                  value={formData.color}
                  onChange={(e) => setFormData(prev => ({ ...prev, color: e.target.value }))}
                  className="color-input"
                />
              </div>

              <div className="form-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={formData.activo}
                    onChange={(e) => setFormData(prev => ({ ...prev, activo: e.target.checked }))}
                  />
                  Cliente Activo
                </label>
              </div>

              <div className="preview-etiqueta">
                <span>Vista previa: </span>
                <span 
                  className="etiqueta-preview"
                  style={{ backgroundColor: formData.color }}
                >
                  {formData.etiqueta}
                </span>
              </div>

              <button className="btn-guardar" onClick={guardarCambios}>
                 Guardar Cambios
              </button>
            </div>
          ) : (
            <div className="no-seleccion">
              <p>Selecciona un cliente de la lista para editarlo o agrega uno nuevo</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Clientes;