// src/components/Comprobadores.jsx
import React, { useState, useEffect } from 'react';
import './Condiciones.css';

// Configuracin para cada proveedor
const PROVIDER_CONFIG = {
  groq: {
    name: 'Groq (Llama de Meta)',
    keyPrefix: 'gsk_',
    placeholder: 'Debes copiar aqu la API de groq (Sigue el tutorial)',
    invalidKeys: [
      'Debes copiar aqu la API de groq (Sigue el tutorial)',
      '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'
    ],
    url: 'https://console.groq.com/keys',
    videoUrl: 'https://www.youtube.com/watch?v=zcB2mMst2A0',
    thumbnailUrl: 'https://img.youtube.com/vi/zcB2mMst2A0/maxresdefault.jpg',
    videoTitle: 'Cmo obtener API Key de Groq'
  },
  openai: {
    name: 'OpenAI (ChatGPT)',
    keyPrefix: 'sk-',
    placeholder: 'Debes copiar aqu la API de OpenAI (Sigue el tutorial)',
    invalidKeys: ['Debes copiar aqu la API de OpenAI (Sigue el tutorial)'],
    url: 'https://platform.openai.com/api-keys',
    videoUrl: 'https://www.youtube.com/watch?v=c098_ICY9s0',
    thumbnailUrl: 'https://img.youtube.com/vi/c098_ICY9s0/maxresdefault.jpg',
    videoTitle: 'Cmo obtener API Key de OpenAI'
  },
  gemini: {
    name: 'Google (Gemini)',
    keyPrefix: 'AI',
    placeholder: 'Debes copiar aqu la API de Google (Sigue el tutorial)',
    invalidKeys: ['Debes copiar aqu la API de Google (Sigue el tutorial)'],
    url: 'https://aistudio.google.com/app/apikey',
    videoUrl: 'https://www.youtube.com/watch?v=hKmBQqh9vaQ',
    thumbnailUrl: 'https://img.youtube.com/vi/hKmBQqh9vaQ/maxresdefault.jpg',
    videoTitle: 'Cmo obtener API Key de Google'
  }
};

const Comprobadores = ({ onValidationComplete }) => {
  const [showModal, setShowModal] = useState(false);
  const [provider, setProvider] = useState('groq'); // Proveedor seleccionado
  const [keyValue, setKeyValue] = useState('');
  const [keyError, setKeyError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const config = PROVIDER_CONFIG[provider]; // Configuracin actual

  // Carga y valida la clave al inicio
  useEffect(() => {
    const initialCheck = async () => {
      // Primero, intenta obtener la ltima seleccin guardada
      let initialProvider = 'groq';
      if (window.electronAPI?.getSeleccion) {
        initialProvider = (await window.electronAPI.getSeleccion()) || 'groq';
        setProvider(initialProvider);
      }
      // Luego, comprueba la clave para ese proveedor
      checkApiKey(initialProvider);
    };
    initialCheck();
  }, []);

  // Cambia de proveedor
  const handleProviderChange = async (newProvider) => {
    setProvider(newProvider);
    setKeyValue(''); // Limpia la clave anterior
    setKeyError(''); // Limpia el error anterior
    if (window.electronAPI) {
      await window.electronAPI.saveSeleccion(newProvider);
    }
    // Comprueba la clave para el nuevo proveedor
    await checkApiKey(newProvider);
  };

  // Verifica la clave API
  const checkApiKey = async (providerToCheck) => {
    try {
      if (!window.electronAPI) return onValidationComplete?.();

      const currentProviderConfig = PROVIDER_CONFIG[providerToCheck];
      const currentKey = await window.electronAPI.getKey();

      const isInvalid =
        !currentKey ||
        currentProviderConfig.invalidKeys.includes(currentKey.trim()) ||
        !currentKey.startsWith(currentProviderConfig.keyPrefix);

      if (isInvalid) {
        setKeyValue('');
        setShowModal(true); // Muestra el modal si la clave es invlida
      } else {
        setShowModal(false); // Oculta si es vlida
        onValidationComplete?.();
      }
    } catch (error) {
      onValidationComplete?.();
    }
  };

  // Guarda la clave API
  const saveKey = async () => {
    setKeyError('');
    if (!keyValue.trim() || !keyValue.startsWith(config.keyPrefix) || config.invalidKeys.includes(keyValue.trim())) {
      setKeyError('Esta API Key no es vlida');
      return;
    }

    try {
      setIsLoading(true);
      await window.electronAPI.saveKey(keyValue.trim());
      setShowModal(false);
      onValidationComplete?.();
    } catch (error) {
      setKeyError('Error al guardar la API Key');
    } finally {
      setIsLoading(false);
    }
  };
  
  // Abre enlace externo
  const openExternalLink = () => {
    window.electronAPI?.openExternalLink(config.url);
  };

  if (!showModal) return null; // Comportamiento original del modal


    // Abrir tutorial externo
  const openTutorial = () => {
    if (window.electronAPI?.openExternalLink) {
      window.electronAPI.openExternalLink(config.videoUrl).catch(error => {
        console.error('Error al abrir el tutorial:', error);
      });
    }
  };

  return (
   
    <div className="modal-backdrop show" style={{ zIndex: 2000 }}>
      <div className="modal-content" style={{ maxWidth: '500px' }}>

        <h2 className="modal-title">Selecciona el Proveedor de IA</h2>
        
        {/* Selector de Proveedor */}
        <select
          value={provider}
          onChange={(e) => handleProviderChange(e.target.value)}
          style={{ width: '100%', padding: '10px', fontSize: '1em', borderRadius: '5px', border: '2px solid #000191', marginBottom: '20px' }}
        >
          <option value="groq">Groq (Llama 4 de Meta) Gratis y pago | Aprox. 1 USD al mes</option>
          <option value="gemini">Google (Gemini Flash) Gratis y pago | Aprox. 4 USD al mes </option> 
          <option value="openai">OpenAI (ChatGPT O4 Mini) Solo pago | Aprox. 10 USD al mes</option>
          
        </select>

        {/* Ttulo dinmico */}
        <div style={{ textAlign: 'center', marginBottom: '20px' }}>
          <h2 style={{ margin: '0', fontSize: '1.8em', fontWeight: '600', color: '#000191' }}>
             API Key de {config.name.split(' ')[0]}
          </h2>
        </div>
        
        {/* Placeholder dinmico */}
        {keyValue === config.placeholder && (
          <div className="api-key-placeholder-highlight" style={{ background: '#e7effc', border: '2px solid #adc9e7', padding: '15px', borderRadius: '8px', marginBottom: '20px', textAlign: 'center' }}>
            <p style={{ margin: '0 0 6px 0', fontSize: '1em', fontWeight: '600', color: '#000191' }}>
              Necesitas configurar tu API Key {config.name}
            </p>
          </div>
        )}

        {/* Mensaje de error */}
        {keyError && (
          <div style={{ background: '#fee', color: '#d63384', padding: '10px', borderRadius: '5px', marginBottom: '15px' }}>
            {keyError}
          </div>
        )}

        {/* Input dinmico */}
        <input
          type="text"
          placeholder={`Copia la API KEY de ${config.name.split(' ')[0]}...`}
          value={keyValue}
          onChange={(e) => setKeyValue(e.target.value)}
          disabled={isLoading}
          style={{ width: '100%', padding: '12px', border: '3px solid #000191', borderRadius: '5px', fontSize: '1em', marginBottom: '15px', boxSizing: 'border-box' }}
        />

          {/* Tutorial Botn*/}
          <div className="tutorial-center-section">
            <button type="button" className="btn-tutorial-main" onClick={openTutorial}>
              <span className="btn-icon" aria-hidden>
                <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round">
                  <circle cx="12" cy="12" r="10" />
                  <polygon points="10 8 16 12 10 16 10 8" />
                </svg>
              </span>
              Ver Tutorial
            </button>
          </div>
        
        {/* Video Tutorial dinmico */}
        <div style={{ background: '#f8f9fa', padding: '15px', borderRadius: '5px', marginBottom: '20px' }}>
          <p style={{ margin: '0 0 10px 0', fontWeight: 'bold' }}> Mira cmo obtener tu API Key:</p>
          <img
            src={config.thumbnailUrl}
            alt={config.videoTitle}
            onClick={openTutorial} // Reutiliza la funcin que ya existe
            style={{
              width: '100%',
              height: '200px', // Mantiene el tamao del iframe
              borderRadius: '5px',
              cursor: 'pointer', // Muestra que es clickeable
              objectFit: 'cover' // Asegura que la imagen cubra el espacio
            }}
          />
        </div>

        {/* Botones de accin */}
        <div style={{ display: 'flex', gap: '10px', justifyContent: 'center' }}>
          <button onClick={openExternalLink} style={{ background: '#6c757d', color: 'white', padding: '10px 20px', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
             Ir a {config.name.split(' ')[0]}
          </button>
          <button onClick={saveKey} disabled={isLoading || !keyValue.trim()} style={{ background: isLoading ? '#ccc' : '#0eda91', color: isLoading ? '#666' : '#000191', padding: '10px 20px', border: 'none', borderRadius: '5px', cursor: isLoading ? 'not-allowed' : 'pointer', fontWeight: 'bold' }}>
            {isLoading ? ' Guardando...' : ' Guardar'}
          </button>
        </div>

      </div>
    </div>
  );
};

export default Comprobadores;