// src/components/WhatsApp.jsx
import React, { useState, useEffect, useRef, use } from 'react';
import Modals from './Modals';
import Comprobadores from './ComprobarSeleccion';
import { useValidador } from '../Validador';
import { useTestigo } from '../Testigo';

const WhatsApp = () => {
  // Hook del validador global
  const {
    appValidated,
    conditionsAccepted,
    apiKeyValid,
    updateApiKeyValid,
  } = useValidador();

  // Hook del testigo global para el estado de WhatsApp
  const { 
    whatsappStatus,
    statusLight,
    isWhatsappConnected, 
    updateWhatsappStatusIndicator
  } = useTestigo();

  // Estados locales del componente
  const [resultado, setResultado] = useState('');
  const [statusUpdateReceived, setStatusUpdateReceived] = useState(false);
  const [versionCheckComplete, setVersionCheckComplete] = useState(false);
  
  // NUEVO: Estado para el switch de activo
  const [estadoActivo, setEstadoActivo] = useState(false);
  const [loadingActivo, setLoadingActivo] = useState(false);
  
  // Estados para modales
  const [showContextModal, setShowContextModal] = useState(false);
  const [showKeyModal, setShowKeyModal] = useState(false);
  const [showRestartModal, setShowRestartModal] = useState(false);
  const [showVersionModal, setShowVersionModal] = useState(false);

  // AGENTE

  const [agenteNumero, setAgenteNumero] = useState(null);
  const [ProveedorIA, setProveedorIA] = useState(null);

  // Ref para el contenedor del QR
  const resultadoRef = useRef(null);

  // NUEVO: Funcin para cargar el estado activo
  const cargarEstadoActivo = async () => {
    try {
      const estado = await window.electronAPI.getActivo();
      setEstadoActivo(estado);
      console.log(' Estado activo cargado:', estado);
    } catch (error) {
      console.error(' Error cargando estado activo:', error);
      setEstadoActivo(false); // Por defecto false en caso de error
    }
  };

  // NUEVO: Funcin para alternar el estado activo
  const handleToggleActivo = async () => {
    if (loadingActivo) return; // Prevenir clics mltiples
    
    setLoadingActivo(true);
    try {
      const resultado = await window.electronAPI.toggleActivo();
      setEstadoActivo(resultado.nuevoEstado);
      console.log(' Estado activo alternado:', resultado);
      
      // Opcional: Mostrar mensaje de confirmacin
      // mostrarTextoResultado(resultado.mensaje);
    } catch (error) {
      console.error(' Error alternando estado activo:', error);
      // Revertir el estado visual si hay error
      await cargarEstadoActivo();
    } finally {
      setLoadingActivo(false);
    }
  };

  // Funcin para mostrar texto en el rea de resultados
  const mostrarTextoResultado = (texto) => {
    setResultado(texto);
    if (resultadoRef.current) {
      resultadoRef.current.innerHTML = '';
    }
  };

  // Funcin para mostrar QR
  const mostrarQR = (qrData) => {
    setResultado('');
    if (resultadoRef.current) {
      resultadoRef.current.innerHTML = '';
      try {
        if (window.QRCode) {
          new window.QRCode(resultadoRef.current, {
            text: qrData,
            width: 200,
            height: 200,
            colorDark: "#000000",
            colorLight: "#ffffff",
            correctLevel: window.QRCode.CorrectLevel.H
          });
          
          const qrMessage = document.createElement('div');
          qrMessage.classList.add('status-text');
          qrMessage.style.textAlign = 'center';
          qrMessage.textContent = 'Escanea el cdigo QR desde la aplicacin de Whatsapp en tu telfono';
          resultadoRef.current.appendChild(qrMessage);
        } else {
          setResultado('QR Code: ' + qrData);
        }
      } catch (e) {
        setResultado('Error al mostrar QR.');
      }
    }
  };

  // Funcin para manejar actualizaciones de estado de WhatsApp
  const handleWhatsAppStatusUpdate = (update) => {
    setStatusUpdateReceived(true);

    if (update.type === 'qr') {
      mostrarQR(update.data);
      updateWhatsappStatusIndicator('Esperando QR');
    } else if (update.type === 'status' || update.type === 'message') {
      mostrarTextoResultado(update.data);
      updateWhatsappStatusIndicator(update.data);
    }
  };

  // Funcin para ejecutar WhatsApp
  const ejecutarWhatsapp = async () => {
    if (!appValidated) {
      mostrarTextoResultado(' Por favor, completa la configuracin inicial antes de continuar.');
      return;
    }

    mostrarTextoResultado('Iniciando...');
    updateWhatsappStatusIndicator('Iniciando...');
    
    try {
      const mensajeInicial = await window.electronAPI.ejecutarWhatsapp();
      console.log('Respuesta de ejecutarWhatsapp:', mensajeInicial);
      
      if (typeof mensajeInicial === 'string' && mensajeInicial !== 'Solicitud de inicio recibida.') {
        mostrarTextoResultado(mensajeInicial);
      }
    } catch (error) {
      console.error('Error al ejecutar WhatsApp:', error);
      mostrarTextoResultado(' Error al solicitar inicio: ' + error.message);
      updateWhatsappStatusIndicator('Error');
    }
  };

  // Funcin para cerrar WhatsApp
  const cerrarWhatsapp = async () => {
    if (!appValidated) {
      mostrarTextoResultado(' Por favor, completa la configuracin inicial antes de continuar.');
      return;
    }

    mostrarTextoResultado('Cerrando...');
    updateWhatsappStatusIndicator('Cerrando...');
    
    try {
      const mensajeCierre = await window.electronAPI.cerrarWhatsapp();
      console.log('Respuesta de cerrarWhatsapp:', mensajeCierre);
      mostrarTextoResultado(mensajeCierre);
      updateWhatsappStatusIndicator('Inactivo');
    } catch (error) {
      console.error('Error al cerrar WhatsApp:', error);
      mostrarTextoResultado(' Error al solicitar cierre: ' + error.message);
      updateWhatsappStatusIndicator('Error');
    }
  };

  // Funcin para reiniciar WhatsApp
  const reiniciarWhatsapp = async () => {
    if (!appValidated) {
      mostrarTextoResultado(' Por favor, completa la configuracin inicial antes de continuar.');
      return;
    }
        
    try {
      mostrarTextoResultado('Reiniciando...');
      updateWhatsappStatusIndicator('Reiniciando');
      const mensajeReinicio = await window.electronAPI.reiniciarWhatsapp();
      console.log('Respuesta de reiniciarWhatsapp:', mensajeReinicio);
      mostrarTextoResultado(mensajeReinicio);
      updateWhatsappStatusIndicator('Inactivo');
      
    } catch (error) {
      console.error('Error al reiniciar WhatsApp:', error);
      mostrarTextoResultado(' Error al reiniciar: ' + error.message);
      updateWhatsappStatusIndicator('Error');
      
    }
  };

  // Handlers para modales
  const handleContextSaved = () => {
    setShowRestartModal(true);
  };

  const handleKeySaved = () => {
    setShowRestartModal(true);
  };



  // Handler para cuando la validacin de API Key se completa
  const handleValidationComplete = () => {
    console.log(' [WhatsApp] API Key validation completed - updating global state...');
    updateApiKeyValid(true);
  };

  // Mostrar mensaje inicial si es necesario
  const showInitialMessage = () => {
    console.log(' [WhatsApp] Verificando si mostrar mensaje inicial:');
    console.log('   - statusUpdateReceived:', statusUpdateReceived);
    console.log('   - versionCheckComplete:', versionCheckComplete);
    console.log('   - appValidated:', appValidated);
    console.log('   - isWhatsappConnected (testigo):', isWhatsappConnected);
    
    if (!statusUpdateReceived && versionCheckComplete && appValidated && !isWhatsappConnected) {
      console.log(' [WhatsApp] Mostrando mensaje inicial');
      mostrarTextoResultado(' Haz clic en "Iniciar WhatsApp" para activar el agente (recuerda haber ajustado el contexto si es necesario).');
    } 
  };


  

  // Effect para configurar listeners de Electron
  useEffect(() => {
    console.log('[WhatsApp] Component mounted. Configuraciones especficas de WhatsApp.jsx...');

        
    if (!window.electronAPI) {
      console.error('electronAPI no est disponible en el contexto de React');
      mostrarTextoResultado(' Error: La aplicacin debe ejecutarse desde Electron');
      return;
    }

    console.log('[WhatsApp] Configurando listener de WhatsApp status updates para UI local...');
    const removeListener = window.electronAPI.onWhatsappStatusUpdate((update) => {
      console.log('[WhatsApp] Recibida actualizacin de WhatsApp (UI local):', update);
      handleWhatsAppStatusUpdate(update);
    });

    setVersionCheckComplete(true);
    console.log('[WhatsApp] Component inicializado correctamente');

    // NUEVO: Cargar estado activo al montar el componente
    cargarEstadoActivo();

    return () => {
      console.log('[WhatsApp] Limpiando listeners de WhatsApp component');
      if (removeListener && typeof removeListener === 'function') {
        removeListener();
      }
    };
  }, []);

  // Effect para mostrar mensaje inicial
  useEffect(() => {
    console.log(' [WhatsApp] useEffect showInitialMessage disparado');
    showInitialMessage();
  }, [statusUpdateReceived, versionCheckComplete, appValidated, isWhatsappConnected]);

  // Effect para mostrar modal de versin al inicio
  useEffect(() => {
    const timer = setTimeout(() => {
      setShowVersionModal(true);
    }, 1000);

    return () => clearTimeout(timer);
  }, []);

  // Determinar si el botn "Iniciar WhatsApp" debe estar deshabilitado
  const isStartButtonDisabled = whatsappStatus !== 'Inactivo';

  //AGENTE*

  useEffect(() => {
  const cargarAgente = async () => {
    const valor = await window.electronAPI.getAgente();
    setAgenteNumero(valor);
    };
    cargarAgente();
    }, []);

    useEffect(() => {
        const intervalo = setInterval(async () => {
            const valor = await window.electronAPI.getSeleccion();
            setProveedorIA(valor.toUpperCase());
        }, 1000); // Cada segundo
        
        return () => clearInterval(intervalo);
    }, []);

  return (
    <>
    
      <Comprobadores onValidationComplete={handleValidationComplete} />

      <div id="mainFunctionsSection" className="content-section active">
        <div className="container">
          <h1>WAI: Agente {agenteNumero || '...'} con {ProveedorIA || '...'}</h1>

          {!appValidated && (
            <div style={{
              background: '#fff3cd',
              color: '#856404',
              padding: '12px 15px',
              borderRadius: '8px',
              marginBottom: '20px',
              border: '1px solid #ffeaa7',
              textAlign: 'center'
            }}>
              <strong> Configuracin pendiente</strong><br />
              Completa la configuracin inicial y acepta las condiciones para continuar
              <div style={{ marginTop: '8px', fontSize: '14px' }}>
                Condiciones: {conditionsAccepted ? '' : ''} | API Key: {apiKeyValid ? '' : ''}
              </div>
            </div>
          )}

          {/* NUEVO: Switch de Estado Activo */}
          <div style={{
            background: '#f8f9fa',
            border: '1px solid #dee2e6',
            borderRadius: '8px',
            padding: '15px',
            marginBottom: '20px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between'
          }}>
            <div>
              <h3 style={{ margin: '0 0 5px 0', fontSize: '16px' }}>Estado del Agente IA</h3>
              <p style={{ margin: 0, fontSize: '14px', color: '#6c757d' }}>
                {estadoActivo ? 'El agente IA est activo y responder mensajes' : 'Activa la IA En el Switch '}
              </p>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
              <span style={{ fontSize: '14px', color: estadoActivo ? '#28a745' : '#6c757d' }}>
                {estadoActivo ? 'Activo' : 'Inactivo'}
              </span>
              <label style={{
                position: 'relative',
                display: 'inline-block',
                width: '50px',
                height: '24px',
                cursor: loadingActivo ? 'not-allowed' : 'pointer'
              }}>
                <input
                  type="checkbox"
                  checked={estadoActivo}
                  onChange={handleToggleActivo}
                  disabled={loadingActivo}
                  style={{ display: 'none' }}
                />
                <span style={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  backgroundColor: estadoActivo ? '#28a745' : '#ccc',
                  borderRadius: '24px',
                  transition: 'background-color 0.3s',
                  opacity: loadingActivo ? 0.6 : 1
                }}>
                  <span style={{
                    position: 'absolute',
                    height: '20px',
                    width: '20px',
                    left: estadoActivo ? '28px' : '2px',
                    bottom: '2px',
                    backgroundColor: 'white',
                    borderRadius: '50%',
                    transition: 'left 0.3s',
                    boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
                  }}></span>
                </span>
              </label>
              {loadingActivo && (
                <span style={{ fontSize: '12px', color: '#6c757d' }}>
                  Actualizando...
                </span>
              )}
            </div>
          </div>
          
          <div className="button-container">
            <button 
              id="ejecutarBtn"
              onClick={ejecutarWhatsapp}
              disabled={isStartButtonDisabled}
              style={{
                opacity: isStartButtonDisabled ? 0.6 : 1,
                cursor: isStartButtonDisabled ? 'not-allowed' : 'pointer'
              }}
            >
              Iniciar WhatsApp
            </button>
            <button 
              id="cerrarBtn"
              onClick={cerrarWhatsapp}
            >
              Cerrar WhatsApp
            </button>
            <button 
              id="reiniciarBtn"
              onClick={reiniciarWhatsapp}
            >
               Reiniciar Sesin
            </button>
            <button 
              id="contextoBtn"
              onClick={() => setShowContextModal(true)}
            >
              Contexto
            </button>
            <button 
              id="editKeyBtn"
              onClick={() => setShowKeyModal(true)}
            >
              Editar API KEY
            </button>
          </div>
          
          <div id="resultado">
            {resultado && (
              <div className="status-text">
                {resultado}
              </div>
            )}
            <div ref={resultadoRef} style={{ textAlign: 'center' }}></div>
          </div>
        </div>
      </div>

      <Modals
        showContextModal={showContextModal}
        showKeyModal={showKeyModal}
        showRestartModal={showRestartModal}
        showVersionModal={showVersionModal}
        onCloseContextModal={() => setShowContextModal(false)}
        onCloseKeyModal={() => setShowKeyModal(false)}
        onCloseRestartModal={() => setShowRestartModal(false)}
        onCloseVersionModal={() => {
          setShowVersionModal(false);
          setVersionCheckComplete(true);
        }}
        onContextSaved={handleContextSaved}
        onKeySaved={handleKeySaved}
      />
    </>
  );
};

export default WhatsApp;