import React, { useState, useEffect } from 'react';
import './Cuenta.css';

const Cuenta = () => {
  const [accountData, setAccountData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [showLogoutConfirm, setShowLogoutConfirm] = useState(false);

  useEffect(() => {
    fetchAccountData();
  }, []);

  const fetchAccountData = async () => {
    setLoading(true);
    setError(null);
    try {
      const data = await window.electronAPI?.getParametros();
      setAccountData(data);
    } catch (err) {
      setError('Error al cargar los datos de la cuenta');
      console.error('Error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = async () => {
    try {
      await window.electronAPI?.cerrarCuenta();
      window.location.reload();
    } catch (err) {
      console.error('Error al cerrar sesin:', err);
    }
  };

  const handleExtendPlan = () => {
    window.open('https://wai.artechclick.com/', '_blank');
  };

  // --- FUNCIN AUXILIAR AADIDA ---
  /**
   * Verifica si una fecha es ms de un ao en el futuro.
   * Maneja el formato DD/MM/YYYY como en tu ejemplo "01/10/2055".
   */
  const isDateMoreThanOneYearInFuture = (dateString) => {
    if (!dateString) return false;

    let date;
    
    // Primero intentamos parsear el formato DD/MM/YYYY
    const parts = dateString.split('/');
    if (parts.length === 3) {
        // parts[0] = DD, parts[1] = MM, parts[2] = YYYY
        // new Date(YYYY, MM-1, DD)
        date = new Date(parts[2], parseInt(parts[1]) - 1, parseInt(parts[0]));
    } else {
        // Si no, intentamos el parseo estndar (ej. ISO 2055-10-01)
        date = new Date(dateString);
    }

    // Si la fecha no es vlida, no podemos comparar
    if (isNaN(date.getTime())) {
        console.warn("Formato de fecha invlido, no se pudo parsear:", dateString);
        return false;
    }

    const now = new Date();
    const oneYearFromNow = new Date();
    oneYearFromNow.setFullYear(now.getFullYear() + 1);
    
    // Retorna true si la fecha del plan es MS ALL de un ao desde hoy
    return date > oneYearFromNow;
  };
  // --- FIN DE LA FUNCIN AUXILIAR ---

  return (
    <div className="cuenta-container">
      <div className="cuenta-content">
        {/* Header */}
        <div className="cuenta-header">
          <div className="cuenta-header-icon">
            <span className="cuenta-icon"></span>
          </div>
          <h1 className="cuenta-title">Mi Cuenta</h1>
          <p className="cuenta-subtitle">Gestiona tu informacin y configuracin</p>
        </div>

        {/* Content Card */}
        <div className="cuenta-card">
          {loading && (
            <div className="cuenta-loading">
              <div className="cuenta-spinner"></div>
              <span className="cuenta-loading-text">Cargando informacin...</span>
            </div>
          )}

          {error && (
            <div className="cuenta-error">
              <p className="cuenta-error-text">{error}</p>
              <button 
                onClick={fetchAccountData}
                className="cuenta-error-retry"
              >
                Intentar de nuevo
              </button>
            </div>
          )}

          {accountData && (
            // --- LGICA DE VISUALIZACIN AADIDA ---
            // Usamos una IIFE para definir variables antes de renderizar
            (() => {
              // Por defecto es Mensual y la fecha que llega
              let planDisplay = "Mensual";
              let fechaDisplay = accountData.activa?.fecha;
              
              if (accountData.activa?.fecha) {
                  // Comprobamos si la fecha es ms de un ao en el FUTURO
                  const esIlimitado = isDateMoreThanOneYearInFuture(accountData.activa.fecha);
                  
                  // Si es as, cambiamos las variables
                  if (esIlimitado) {
                      planDisplay = "Ilimitado";
                      fechaDisplay = "Ilimitado";
                  }
              }

              // Devolvemos el JSX con las variables correctas
              return (
                <div className="cuenta-info">
                  {/* Account Information */}
                  <div className="cuenta-data">
                    <h2 className="cuenta-section-title">Informacin de la Cuenta</h2>
                    
                    {/* Email */}
                    <div className="cuenta-item">
                      <span className="cuenta-item-icon"></span>
                      <div className="cuenta-item-content">
                        <h3 className="cuenta-item-title">Correo Electrnico</h3>
                        <p className="cuenta-item-value">{accountData.cuenta?.correo}</p>
                      </div>
                    </div>

                    {/* Plan --- MODIFICADO --- */}
                    <div className="cuenta-item">
                      <span className="cuenta-item-icon"></span>
                      <div className="cuenta-item-content">
                        <h3 className="cuenta-item-title">Plan</h3>
                        <p className="cuenta-item-value cuenta-item-capitalize">
                          {planDisplay}
                        </p>
                      </div>
                    </div>

                    {/* Plan Status --- MODIFICADO --- */}
                    <div className="cuenta-item">
                      <span className="cuenta-item-icon"></span>
                      <div className="cuenta-item-content">
                        <h3 className="cuenta-item-title">Estado del Plan</h3>
                        <div className="cuenta-status">
                          <span className={`cuenta-status-dot ${accountData.activa?.ok ? 'cuenta-status-active' : 'cuenta-status-inactive'}`}></span>
                          <span className={`cuenta-status-text ${accountData.activa?.ok ? 'cuenta-status-active-text' : 'cuenta-status-inactive-text'}`}>
                            {accountData.activa?.ok ? 'Plan Activo' : 'Plan Inactivo'}
                          </span>
                        </div>
                        {accountData.activa?.fecha && (
                          <p className="cuenta-status-date">
                            ltima actualizacin: {fechaDisplay}
                          </p>
                        )}
                      </div>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="cuenta-actions">
                    <button
                      onClick={handleExtendPlan}
                      className="cuenta-btn cuenta-btn-extend"
                    >
                      <span className="cuenta-btn-icon"></span>
                      Comprar una licencia extra (-50%)
                    </button>

                    <button
                      onClick={() => setShowLogoutConfirm(true)}
                      className="cuenta-btn cuenta-btn-logout"
                    >
                      <span className="cuenta-btn-icon"></span>
                      Cerrar Sesin
                    </button>
                  </div>
                </div>
              );
            })()
            // --- FIN DE LA LGICA DE VISUALIZACIN ---
          )}
        </div>
      </div>

      {/* Logout Confirmation Modal */}
      {showLogoutConfirm && (
        <div className="cuenta-modal-overlay">
          <div className="cuenta-modal">
            <div className="cuenta-modal-content">
              <div className="cuenta-modal-header">
                <div className="cuenta-modal-warning-icon">
                  <span></span>
                </div>
                <h3 className="cuenta-modal-title">Cerrar sesin?</h3>
                <p className="cuenta-modal-description">
                  Al cerrar sesin tendrs que registrar nuevamente tu clave para acceder a la aplicacin.
                </p>
              </div>
              
              <div className="cuenta-modal-actions">
                <button
                  onClick={() => setShowLogoutConfirm(false)}
                  className="cuenta-modal-btn cuenta-modal-btn-cancel"
                >
                  Cancelar
                </button>
                <button
                  onClick={handleLogout}
                  className="cuenta-modal-btn cuenta-modal-btn-confirm"
                >
                  Cerrar Sesin
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Cuenta;