// src/components/PredefinedMessages.jsx
import React, { useState, useEffect, useCallback } from 'react';
import './PredefinedMessages.css'; // Aadir esta lnea

const PredefinedMessages = () => {
  // Estados para el formulario
  const [keywordInput, setKeywordInput] = useState('');
  const [messageText, setMessageText] = useState('');
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [currentKeywords, setCurrentKeywords] = useState([]);
  
  // Estados para mensajes guardados
  const [savedMessages, setSavedMessages] = useState({});
  const [loading, setLoading] = useState(false);

  // Tipos de archivo vlidos
  const validFileTypes = ['pdf', 'mp3', 'ogg', 'png', 'jpg', 'jpeg', 'webp', 'gif', 'mp4', 'avi', 'mov', 'mkv', 'webm'];

  // Funcin para validar tipo de archivo - memoizada
  const isValidFileType = useCallback((file) => {
    const fileExtension = file.name.split('.').pop().toLowerCase();
    return validFileTypes.includes(fileExtension);
  }, []);

  // SOLUCIN: Usar Electron Dialog en lugar de alert()
  const showCustomAlert = useCallback(async (message) => {
    if (window.electronAPI?.showMessageBox) {
      // Usar el dialog nativo de Electron (NO causa prdida de foco)
      await window.electronAPI.showMessageBox({
        type: 'info',
        title: 'Informacin',
        message: message,
        buttons: ['OK']
      });
    } else {
      // Fallback para desarrollo (navegador)
      alert(message);
    }
  }, []);

  // SOLUCIN: Usar Electron Dialog para confirmaciones
  const showCustomConfirm = useCallback(async (message, callback) => {
    if (window.electronAPI?.showMessageBox) {
      // Usar el dialog nativo de Electron
      const result = await window.electronAPI.showMessageBox({
        type: 'question',
        title: 'Confirmacin',
        message: message,
        buttons: ['S', 'No'],
        defaultId: 0,
        cancelId: 1
      });
      callback(result.response === 0); // 0 = "S", 1 = "No"
    } else {
      // Fallback para desarrollo
      const confirmed = window.confirm(message);
      callback(confirmed);
    }
  }, []);

  // Cargar mensajes predeterminados
  const loadPredeterminedMessages = useCallback(async () => {
    if (!window.electronAPI?.getPredeterminedMessages) {
      console.log('electronAPI no disponible, usando datos de ejemplo');
      setSavedMessages({});
      return;
    }

    try {
      setLoading(true);
      console.log(' Cargando mensajes predeterminados...');
      const messages = await window.electronAPI.getPredeterminedMessages();
      console.log(' Mensajes cargados:', messages);
      setSavedMessages(messages || {});
    } catch (error) {
      console.error(' Error cargando mensajes:', error);
      await showCustomAlert('Error al cargar los mensajes predeterminados.');
      setSavedMessages({});
    } finally {
      setLoading(false);
    }
  }, [showCustomAlert]);

  // Cargar mensajes al montar el componente
  useEffect(() => {
    loadPredeterminedMessages();
  }, [loadPredeterminedMessages]);

  // Manejar seleccin de archivos - OPTIMIZADO
  const handleFileSelection = useCallback((event) => {
    const files = Array.from(event.target.files);
    
    // Procesar todos los archivos de una vez
    const validFiles = [];
    const invalidFiles = [];
    
    files.forEach(file => {
      if (!isValidFileType(file)) {
        invalidFiles.push(file.name);
        return;
      }
      
      // Solo agregar si no existe ya
      if (!selectedFiles.some(f => f.name === file.name && f.size === file.size)) {
        validFiles.push(file);
      }
    });
    
    // Mostrar errores de archivos invlidos
    if (invalidFiles.length > 0) {
      showCustomAlert(`Los siguientes archivos no son tipos permitidos: ${invalidFiles.join(', ')}. Solo se permiten .pdf, .mp3, .ogg, .png, .jpg, .jpeg, .webp, .gif.`);
    }
    
    // Una sola actualizacin de estado
    if (validFiles.length > 0) {
      setSelectedFiles(prev => [...prev, ...validFiles]);
    }
    
    // Limpiar el input
    event.target.value = '';
  }, [selectedFiles, isValidFileType, showCustomAlert]);

  // Remover archivo seleccionado
  const removeSelectedFile = useCallback((index) => {
    setSelectedFiles(prev => prev.filter((_, i) => i !== index));
  }, []);

  // Agregar keyword tag - OPTIMIZADO
  const addKeywordTag = useCallback((keyword = null) => {
    const keywordText = keyword || keywordInput.trim();
    
    if (keywordText && !currentKeywords.includes(keywordText)) {
      setCurrentKeywords(prev => [...prev, keywordText]);
      if (!keyword) {
        setKeywordInput('');
      }
    }
  }, [keywordInput, currentKeywords]);

  // Manejar input de palabras clave
  const handleKeywordInput = useCallback((event) => {
    if (event.key === ',' || event.key === 'Enter') {
      event.preventDefault();
      addKeywordTag();
    }
  }, [addKeywordTag]);

  // Remover keyword tag
  const removeKeywordTag = useCallback((keyword) => {
    setCurrentKeywords(prev => prev.filter(kw => kw !== keyword));
  }, []);

  // Manejar blur del input de keywords
  const handleKeywordBlur = useCallback(() => {
    if (keywordInput.trim()) {
      addKeywordTag();
    }
  }, [keywordInput, addKeywordTag]);

  // Limpiar formulario
  const clearForm = useCallback(() => {
    setCurrentKeywords([]);
    setKeywordInput('');
    setMessageText('');
    setSelectedFiles([]);
  }, []);

  // Guardar mensaje predeterminado - CORREGIDO
  const savePredeterminedMessage = useCallback(async () => {
    // Crear keywords finales sin modificar el estado inmediatamente
    const finalKeywords = keywordInput.trim() 
      ? [...currentKeywords, keywordInput.trim()]
      : currentKeywords;

    // Validaciones usando keywords finales
    if (finalKeywords.length === 0) {
      await showCustomAlert('Por favor, introduce al menos una palabra clave.');
      return;
    }
    
    if (messageText.trim() === '' && selectedFiles.length === 0) {
      await showCustomAlert('Por favor, introduce un mensaje de texto o selecciona al menos un archivo.');
      return;
    }

    if (!window.electronAPI?.savePredeterminedMessage) {
      await showCustomAlert('Funcin no disponible en modo desarrollo');
      return;
    }

    setLoading(true);

    try {
      console.log(' Guardando mensaje predeterminado...');
      
      // Convertir archivos a ArrayBuffer
      const filesToSave = await Promise.all(selectedFiles.map(async file => {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.onload = () => {
            if (reader.result instanceof ArrayBuffer) {
              resolve({
                name: file.name,
                type: file.type,
                arrayBuffer: reader.result
              });
            } else {
              reject(new Error('FileReader did not return an ArrayBuffer.'));
            }
          };
          reader.onerror = (error) => {
            reject(new Error(`Error reading file ${file.name}: ${error.target.error.message}`));
          };
          reader.readAsArrayBuffer(file);
        });
      }));

      const predeterminedMessage = {
        keywords: finalKeywords,
        text: messageText.trim(),
        files: filesToSave
      };

      console.log(' Enviando mensaje:', { 
        keywords: predeterminedMessage.keywords, 
        text: predeterminedMessage.text,
        filesCount: predeterminedMessage.files.length 
      });

      await window.electronAPI.savePredeterminedMessage(predeterminedMessage);
      
      await showCustomAlert('Mensaje predeterminado guardado exitosamente.');
      
      // Limpiar formulario
      clearForm();
      
      // Recargar mensajes
      await loadPredeterminedMessages();
      
    } catch (error) {
      console.error(' Error guardando mensaje:', error);
      await showCustomAlert('Error al guardar el mensaje predeterminado: ' + (error.message || 'Error desconocido. Intenta de nuevo.'));
    } finally {
      setLoading(false);
    }
  }, [keywordInput, currentKeywords, messageText, selectedFiles, showCustomAlert, clearForm, loadPredeterminedMessages]);

  // Eliminar mensaje predeterminado
  const deletePredeterminedMessage = useCallback(async (keyId, keywords) => {
    if (!window.electronAPI?.deletePredeterminedMessage) {
      await showCustomAlert('Funcin no disponible en modo desarrollo');
      return;
    }

    await showCustomConfirm(
      `Ests seguro de que quieres eliminar el grupo de palabras clave "${keywords.join(', ')}"?`,
      async (confirmed) => {
        if (confirmed) {
          try {
            setLoading(true);
            console.log(' Eliminando mensaje:', keyId);
            
            await window.electronAPI.deletePredeterminedMessage(keyId);
            await showCustomAlert('Mensaje predeterminado eliminado.');
            
            // Recargar mensajes
            await loadPredeterminedMessages();
            
          } catch (error) {
            console.error(' Error eliminando mensaje:', error);
            await showCustomAlert('Error al eliminar el mensaje predeterminado. Intenta de nuevo.');
          } finally {
            setLoading(false);
          }
        }
      }
    );
  }, [showCustomAlert, showCustomConfirm, loadPredeterminedMessages]);

  return (
    <div id="predefinedMessagesSection" className="content-section active">
      <div className="container predefined-messages-container-margin">
        <div style={{ height: '200px', backgroundColor: 'rgba(255, 255, 255, 0)' }}></div>
        
        <h2>Mensajes Predeterminados</h2>
        <p>Define respuestas automticas para palabras clave o frases especficas. Puedes incluir texto y archivos.</p>

        <div className="predefined-messages-layout">
          {/* Seccin para agregar mensajes */}
          <div className="predefined-messages-add">
            {/* Input de palabras clave */}
            <div className="form-group">
              <label htmlFor="keywordGroupInput">
                Palabras Clave (separadas por comas, ej: horario, abierto, atencion):
              </label>
              <div id="keywordInputContainer" className="keyword-input-container">
                {/* Tags de keywords */}
                {currentKeywords.map((keyword, index) => (
                  <span key={index} className="keyword-tag">
                    {keyword}
                    <button
                      type="button"
                      className="remove-tag"
                      onClick={() => removeKeywordTag(keyword)}
                    >
                      
                    </button>
                  </span>
                ))}
                
                {/* Input para nuevas keywords */}
                <input
                  type="text"
                  id="keywordGroupInput"
                  placeholder="ej: horario, abierto, atencion"
                  value={keywordInput}
                  onChange={(e) => setKeywordInput(e.target.value)}
                  onKeyDown={handleKeywordInput}
                  onBlur={handleKeywordBlur}
                  disabled={loading}
                />
              </div>
            </div>

            {/* Textarea de mensaje */}
            <div className="form-group">
              <label htmlFor="messageTextInput">
                Mensaje de Texto Complementario (opcional):
              </label>
              <textarea
                id="messageTextInput"
                rows="3"
                placeholder="Este es el texto que se enviar junto al archivo..."
                value={messageText}
                onChange={(e) => setMessageText(e.target.value)}
                disabled={loading}
              />
            </div>

            {/* Seccin de carga de archivos */}
            <div className="file-upload-section">
              <p>Aadir Archivos (audio, imagen, PDF, video):</p>
              <input
                type="file"
                id="mediaFileInput"
                accept=".pdf,.mp3,.ogg,.png,.jpg,.jpeg,.webp,.gif,.mp4,.avi,.mov,.mkv,.webm"
                multiple
                onChange={handleFileSelection}
                disabled={loading}
              />
              
              {/* Lista de archivos seleccionados */}
              <ul id="uploadedFilesList">
                {selectedFiles.map((file, index) => (
                  <li key={index}>
                    <span className="file-name">{file.name}</span>
                    <button
                      type="button"
                      className="remove-file-btn"
                      onClick={() => removeSelectedFile(index)}
                      disabled={loading}
                    >
                      
                    </button>
                  </li>
                ))}
              </ul>
            </div>

            {/* Botn de guardar */}
            <button
              id="addPredeterminedMessageBtn"
              className="save-button"
              onClick={savePredeterminedMessage}
              disabled={loading}
            >
              {loading ? 'Guardando...' : 'Guardar Mensaje Predeterminado'}
            </button>
          </div>

          {/* Seccin de mensajes guardados */}
          <div className="predefined-messages-saved">
            <h3>Grupos Predeterminados Guardados</h3>
            <div id="savedPredeterminedGroups">
              {loading ? (
                <p style={{ textAlign: 'center', color: 'var(--color-text-gray)' }}>
                  Cargando mensajes...
                </p>
              ) : Object.keys(savedMessages).length === 0 ? (
                <p 
                  id="noPredeterminedMessages"
                  style={{ color: 'var(--color-text-gray)', textAlign: 'center' }}
                >
                  HAZ CLICK AQU PARA VER LOS MENSAJES PREDETERMINADOS GUARDADOS
                </p>
              ) : (
                // Renderizar mensajes guardados
                Object.entries(savedMessages).map(([keyId, group]) => (
                  <div key={keyId} className="predetermined-group-item" data-key-id={keyId}>
                    <h4>
                      Palabras Clave:{' '}
                      {group.keywords.map((keyword, index) => (
                        <span key={index} className="keyword-tag">
                          {keyword}
                          <button className="remove-tag" data-keyword={keyword}></button>
                        </span>
                      ))}
                    </h4>
                    
                    {group.text && (
                      <p><strong>Mensaje de Texto:</strong> {group.text}</p>
                    )}
                    
                    {group.files && group.files.length > 0 && (
                      <>
                        <p><strong>Archivos Adjuntos:</strong></p>
                        <ul>
                          {group.files.map((file, index) => (
                            <li key={index}>{file.name} ({file.type})</li>
                          ))}
                        </ul>
                      </>
                    )}
                    
                    <div className="group-actions">
                      <button 
                        className="delete-group-btn"
                        onClick={() => deletePredeterminedMessage(keyId, group.keywords)}
                        disabled={loading}
                      >
                        {loading ? 'Eliminando...' : 'Eliminar'}
                      </button>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PredefinedMessages;