import React, { useState, useEffect } from 'react';
import './Remarketing.css'; // Asume que tendrs un archivo CSS

const Remarketing = () => {
    // Estados principales
    const [configuracion, setConfiguracion] = useState({
        mensaje: { text: '', files: [] },
        activo: false,
        horario: { inicio: '09:00:00', fin: '18:00:00' },
        rango: 30 // minutos
    });
    
    // Estados para el cronmetro de rango
    const [rangoHoras, setRangoHoras] = useState(0);
    const [rangoMinutos, setRangoMinutos] = useState(30);
    
    const [remarketingActivo, setRemarketingActivo] = useState(false);
    const [loading, setLoading] = useState(false);
    const [mensaje, setMensaje] = useState('');
    const [archivosSeleccionados, setArchivosSeleccionados] = useState([]);

    // Cargar configuracin al montar el componente
    useEffect(() => {
        cargarConfiguracion();
    }, []);

    // Efecto para convertir minutos a horas y minutos al cargar
    useEffect(() => {
        const horas = Math.floor(configuracion.rango / 60);
        const minutos = configuracion.rango % 60;
        setRangoHoras(horas);
        setRangoMinutos(minutos);
    }, [configuracion.rango]);

    // Efecto para actualizar el rango cuando cambian horas o minutos
    useEffect(() => {
        const totalMinutos = (rangoHoras * 60) + rangoMinutos;
        setConfiguracion(prev => ({
            ...prev,
            rango: totalMinutos
        }));
    }, [rangoHoras, rangoMinutos]);

    // Funcin para cargar la configuracin existente
    const cargarConfiguracion = async () => {
        try {
            setLoading(true);
            
            // Cargar configuracin
            const config = await window.electronAPI.getRemarketing();
            
            if (config && config.activo !== undefined) {
                setConfiguracion(config);
            }
            
            // Verificar estado actual del remarketing por separado
            await verificarEstadoRemarketing();
            
        } catch (error) {
            console.error('Error cargando configuracin:', error);
            mostrarMensaje('Error al cargar la configuracin', 'error');
        } finally {
            setLoading(false);
        }
    };

    // Verificar si el remarketing est activo
    const verificarEstadoRemarketing = async () => {
        try {
            const activo = await window.electronAPI.estaRemarketingActivo();
            setRemarketingActivo(activo);
        } catch (error) {
            console.error('Error verificando estado:', error);
            setRemarketingActivo(false); // Fallback a inactivo en caso de error
        }
    };

    // Manejar archivos seleccionados
    const manejarArchivos = (event) => {
        const files = Array.from(event.target.files);
        const processedFiles = files.map(file => ({
            name: file.name,
            type: file.type,
            arrayBuffer: null // Se procesar al guardar
        }));
        setArchivosSeleccionados(processedFiles);
    };

    // Procesar archivos para envo
    const procesarArchivos = async (files) => {
        const processedFiles = [];
        
        for (const file of files) {
            if (file.arrayBuffer === null) {
                // Es un archivo nuevo, necesita procesarse
                const fileInput = document.querySelector('input[type="file"]');
                const realFile = Array.from(fileInput.files).find(f => f.name === file.name);
                
                if (realFile) {
                    const arrayBuffer = await realFile.arrayBuffer();
                    processedFiles.push({
                        ...file,
                        arrayBuffer: arrayBuffer
                    });
                }
            } else {
                // Es un archivo existente
                processedFiles.push(file);
            }
        }
        
        return processedFiles;
    };

    // Guardar configuracin
    const guardarConfiguracion = async () => {
        try {
            setLoading(true);
            
            // Validaciones
            if (!configuracion.mensaje.text.trim()) {
                mostrarMensaje('El mensaje no puede estar vaco', 'error');
                return;
            }

            if (configuracion.rango < 1) {
                mostrarMensaje('El rango debe ser mayor a 0 minutos', 'error');
                return;
            }

            if (configuracion.rango > 1410) {
                mostrarMensaje('El rango no puede ser mayor a 23 horas y 30 minutos', 'error');
                return;
            }

            // Procesar archivos
            const processedFiles = await procesarArchivos(archivosSeleccionados);
            
            const messageData = {
                text: configuracion.mensaje.text,
                files: processedFiles
            };

            // Guardar configuracin
            const resultado = await window.electronAPI.saveRemarketing(
                messageData,
                configuracion.activo,
                configuracion.horario,
                configuracion.rango
            );

            if (resultado.success) {
                mostrarMensaje('Configuracin guardada exitosamente', 'success');
                
                // Si se configur como activo y se guard exitosamente, actualizar el estado
                if (configuracion.activo) {
                    setRemarketingActivo(true);
                } else {
                    // Si se desactiv en configuracin, tambin desactivar el remarketing
                    await verificarEstadoRemarketing();
                }
            }

        } catch (error) {
            console.error('Error guardando configuracin:', error);
            mostrarMensaje('Error al guardar la configuracin', 'error');
        } finally {
            setLoading(false);
        }
    };

    // Toggle del remarketing activo/inactivo
    const toggleRemarketing = async () => {
        try {
            setLoading(true);
            
            // Verificar que existe configuracin antes de activar
            if (!remarketingActivo && !configuracion.mensaje.text.trim()) {
                mostrarMensaje('Primero debes configurar y guardar un mensaje', 'error');
                return;
            }
            
            const nuevoEstado = !remarketingActivo;
            
            const resultado = await window.electronAPI.toggleRemarketing(nuevoEstado);
            
            if (resultado.success) {
                setRemarketingActivo(nuevoEstado);
                mostrarMensaje(resultado.message, 'success');
                
                // Si se est activando, tambin actualizar la configuracin local
                if (nuevoEstado) {
                    setConfiguracion(prev => ({
                        ...prev,
                        activo: true
                    }));
                }
            } else {
                mostrarMensaje('Error al cambiar el estado del remarketing', 'error');
            }

        } catch (error) {
            console.error('Error toggle remarketing:', error);
            mostrarMensaje('Error al cambiar el estado del remarketing', 'error');
        } finally {
            setLoading(false);
        }
    };

    // Mostrar mensaje temporal
    const mostrarMensaje = (texto, tipo) => {
        setMensaje({ texto, tipo });
        setTimeout(() => setMensaje(''), 5000);
    };

    // Remover archivo
    const removerArchivo = (index) => {
        const nuevosArchivos = archivosSeleccionados.filter((_, i) => i !== index);
        setArchivosSeleccionados(nuevosArchivos);
    };

    return (
        <div className="remarketing-container">
            <div className="remarketing-header">
                <h2>Configuracin de Remarketing</h2>
                <div className="remarketing-status">
                    <span className={`status-indicator ${remarketingActivo ? 'active' : 'inactive'}`}>
                        {remarketingActivo ? ' Activo' : ' Inactivo'}
                    </span>
                    <label className="switch">
                        <input 
                            type="checkbox" 
                            checked={remarketingActivo}
                            onChange={toggleRemarketing}
                            disabled={loading}
                        />
                        <span className="slider round"></span>
                    </label>
                </div>
            </div>

            {mensaje && (
                <div className={`mensaje ${mensaje.tipo}`}>
                    {mensaje.texto}
                </div>
            )}

            <div className="remarketing-form">
                {/* Configuracin bsica */}
                <div className="form-section">
                    <h3>Configuracin Bsica</h3>
                    
                    <div className="form-group">
                        <label>
                            <input
                                type="checkbox"
                                checked={configuracion.activo}
                                onChange={(e) => setConfiguracion(prev => ({
                                    ...prev,
                                    activo: e.target.checked
                                }))}
                            />
                            Activar remarketing automtico al guardar
                        </label>
                    </div>

                    <div className="form-group">
                        <label>Tiempo de espera:</label>
                        <div className="rango-tiempo-container">
                            <div className="tiempo-input-group">
                                <input
                                    type="number"
                                    min="0"
                                    max="23"
                                    value={rangoHoras}
                                    onChange={(e) => {
                                        const valor = parseInt(e.target.value) || 0;
                                        if (valor <= 23) {
                                            setRangoHoras(valor);
                                        }
                                    }}
                                    placeholder="0"
                                />
                                <span className="tiempo-label">horas</span>
                            </div>
                            <div className="tiempo-separador">:</div>
                            <div className="tiempo-input-group">
                                <input
                                    type="number"
                                    min="0"
                                    max="59"
                                    value={rangoMinutos}
                                    onChange={(e) => {
                                        const valor = parseInt(e.target.value) || 0;
                                        // Validar que no exceda 23h 30min
                                        if (rangoHoras === 23 && valor > 30) {
                                            setRangoMinutos(30);
                                        } else if (valor <= 59) {
                                            setRangoMinutos(valor);
                                        }
                                    }}
                                    placeholder="30"
                                />
                                <span className="tiempo-label">minutos</span>
                            </div>
                        </div>
                        <small>Tiempo de espera antes de enviar el mensaje de remarketing (mximo: 23h 30min)</small>
                    </div>
                </div>

                {/* Horario */}
                <div className="form-section">
                    <h3>Horario de Funcionamiento</h3>
                    
                    <div className="horario-group">
                        <div className="form-group">
                            <label>Hora de inicio:</label>
                            <input
                                type="time"
                                step="1"
                                value={configuracion.horario.inicio}
                                onChange={(e) => setConfiguracion(prev => ({
                                    ...prev,
                                    horario: { ...prev.horario, inicio: e.target.value }
                                }))}
                            />
                        </div>

                        <div className="form-group">
                            <label>Hora de fin:</label>
                            <input
                                type="time"
                                step="1"
                                value={configuracion.horario.fin}
                                onChange={(e) => setConfiguracion(prev => ({
                                    ...prev,
                                    horario: { ...prev.horario, fin: e.target.value }
                                }))}
                            />
                        </div>
                    </div>
                </div>

                {/* Mensaje */}
                <div className="form-section">
                    <h3>Mensaje de Remarketing</h3>
                    
                    <div className="form-group">
                        <label>Texto del mensaje:</label>
                        <textarea
                            value={configuracion.mensaje.text}
                            onChange={(e) => setConfiguracion(prev => ({
                                ...prev,
                                mensaje: { ...prev.mensaje, text: e.target.value }
                            }))}
                            placeholder="Escribe el mensaje que se enviar a los clientes..."
                            rows="4"
                        />
                    </div>

                    {/* Archivos */}
                    <div className="form-group">
                        <label>Archivos adjuntos (opcional):</label>
                        <input
                            type="file"
                            multiple
                            onChange={manejarArchivos}
                            accept="image/*,video/*,audio/*,.pdf,.doc,.docx"
                        />
                        
                        {archivosSeleccionados.length > 0 && (
                            <div className="archivos-lista">
                                <h4>Archivos seleccionados:</h4>
                                {archivosSeleccionados.map((archivo, index) => (
                                    <div key={index} className="archivo-item">
                                        <span>{archivo.name}</span>
                                        <button 
                                            type="button"
                                            onClick={() => removerArchivo(index)}
                                            className="remove-btn"
                                        >
                                            
                                        </button>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* Archivos existentes */}
                {configuracion.mensaje.files && configuracion.mensaje.files.length > 0 && (
                    <div className="form-section">
                        <h3>Archivos Guardados</h3>
                        <div className="archivos-existentes">
                            {configuracion.mensaje.files.map((archivo, index) => (
                                <div key={index} className="archivo-existente">
                                    <span>{archivo.name}</span>
                                    <span className="archivo-tipo">{archivo.type}</span>
                                </div>
                            ))}
                        </div>
                    </div>
                )}

                {/* Botones de accin */}
                <div className="form-actions">
                    <button 
                        onClick={guardarConfiguracion}
                        disabled={loading}
                        className="save-btn"
                    >
                        {loading ? 'Guardando...' : 'Guardar Configuracin'}
                    </button>

                    <button 
                        onClick={cargarConfiguracion}
                        disabled={loading}
                        className="reload-btn"
                    >
                        Recargar
                    </button>
                </div>

                {/* Informacin adicional */}
                <div className="info-section">
                    <h3> Informacin</h3>
                    <ul>
                        <li>El remarketing se ejecuta automticamente cada 2 minutos</li>
                        <li>Solo afecta a clientes con etiqueta "Nuevo"</li>
                        <li>Los clientes se actualizan a etiqueta "Contactado" despus del envo</li>
                        <li>Respeta el horario configurado</li>
                        <li>El rango de tiempo se cuenta desde el registro del cliente</li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Remarketing;