// src/components/Modals.jsx
import React, { useState, useEffect, useRef } from 'react';

import './Modals.css';

// Configuracin compartida para ambos modales.
const KEY_PROVIDER_CONFIG = {
  groq: {
    name: 'Groq',
    url: 'https://console.groq.com/keys',
  },
  openai: {
    name: 'OpenAI',
    url: 'https://platform.openai.com/api-keys',
  },
  gemini: {
    name: 'Google (Gemini)',
    url: 'https://aistudio.google.com/app/apikey',
  },
};

const Modals = ({ 
  showContextModal, 
  showKeyModal, 
  showRestartModal, 
  showVersionModal,
  onCloseContextModal,
  onCloseKeyModal,
  onCloseRestartModal,
  onCloseVersionModal,
  onContextSaved,
  onKeySaved
}) => {
  // Estados para modal de contexto
  const [contextProvider, setContextProvider] = useState('groq');
  const [contextoValue, setContextoValue] = useState('');
  const [contextoError, setContextoError] = useState('');
  const [contextoLoading, setContextoLoading] = useState(false);
  const [contextNotification, setContextNotification] = useState('');
  const contextNotificationTimerRef = useRef(null);

  // Estados para modal de key
  const [keyProvider, setKeyProvider] = useState('groq');
  const [keyValue, setKeyValue] = useState('');
  const [keyError, setKeyError] = useState('');
  const [keyLoading, setKeyLoading] = useState(false);
  const [keyNotification, setKeyNotification] = useState('');
  const keyNotificationTimerRef = useRef(null);

  // Estados para modal de versin
  const [versionTitle, setVersionTitle] = useState('Verificando versin...');
  const [versionMessage, setVersionMessage] = useState('');
  const [versionButtonText, setVersionButtonText] = useState('');
  const [versionButtonClass, setVersionButtonClass] = useState('');
  const [versionButtonAction, setVersionButtonAction] = useState(null);

  // --- LGICA DE INICIALIZACIN DE MODALES ---

  // Inicializar Modal de Contexto
  useEffect(() => {
    if (showContextModal) {
      const initializeContextModal = async () => {
        try {
          const lastProvider = (await window.electronAPI.getSeleccion?.()) || 'groq';
          setContextProvider(lastProvider);
          loadContexto(lastProvider); // Carga el contexto del proveedor correcto
        } catch (error) {
          setContextoError("Error al cargar la configuracin del contexto.");
        }
      };
      initializeContextModal();
    }
    return () => clearTimeout(contextNotificationTimerRef.current);
  }, [showContextModal]);

  // Inicializar Modal de Key
  useEffect(() => {
    if (showKeyModal) {
      const initializeKeyModal = async () => {
        try {
          const lastProvider = (await window.electronAPI.getSeleccion?.()) || 'groq';
          setKeyProvider(lastProvider);
          loadKey(lastProvider); // Carga la key del proveedor correcto
        } catch (error) {
          setKeyError("Error al cargar la configuracin de la key.");
        }
      };
      initializeKeyModal();
    }
    return () => clearTimeout(keyNotificationTimerRef.current);
  }, [showKeyModal]);
  
  // Inicializar Modal de Versin
  useEffect(() => {
    if (showVersionModal) {
      handleVersionCheck();
    }
  }, [showVersionModal]);

  // --- FUNCIONES DE CARGA Y GUARDADO (ADAPTADAS POR PROVEEDOR) ---

  // Cargar contexto por proveedor
  const loadContexto = async (provider) => {
    setContextoLoading(true);
    setContextoValue('Cargando...');
    setContextoError('');
    try {
      const contextoActual = await window.electronAPI.getContexto(contextoValue);
      setContextoValue(contextoActual || '');
    } catch (error) {
      setContextoError('Error al cargar el contexto.');
    } finally {
      setContextoLoading(false);
    }
  };
  
  // Cargar key por proveedor
  const loadKey = async (provider) => {
    setKeyLoading(true);
    setKeyValue('Cargando...');
    setKeyError('');
    try {
      const keyActual = await window.electronAPI.getKey(keyValue);
      setKeyValue(keyActual || '');
    } catch (error) {
      setKeyValue('');
      setKeyError('Error al cargar la key.');
    } finally {
      setKeyLoading(false);
    }
  };

  // Guardar contexto por proveedor
  const saveContexto = async () => {
    setContextoError('');
    if (contextoValue.trim() === '') {
      setContextoError('El contexto no puede estar vaco.');
      return;
    }
    try {
      await window.electronAPI.saveContexto(contextoValue);
      onCloseContextModal();
      onContextSaved?.();
    } catch (error) {
      setContextoError('Error al guardar.');
    }
  };

  // Guardar key por proveedor
  const saveKey = async () => {
    setKeyError('');
    if (keyValue.trim() === '') {
      setKeyError('La Key no puede estar vaca.');
      return;
    }
    try {
      await window.electronAPI.saveKey(keyValue);
      onCloseKeyModal();
      onKeySaved?.();
    } catch (error) {
      setKeyError('Error al guardar la key.');
    }
  };

  // --- MANEJADORES DE CAMBIO DE SELECTOR ---

  // Manejador para el selector de contexto
  const handleContextProviderChange = async (newProvider) => {
    clearTimeout(contextNotificationTimerRef.current);
    setContextProvider(newProvider);
    await window.electronAPI.saveSeleccion(newProvider);
    loadContexto(newProvider);

    const providerName = KEY_PROVIDER_CONFIG[newProvider].name;
    setContextNotification(` Se ha fijado ${providerName} como proveedor de IA`);
    contextNotificationTimerRef.current = setTimeout(() => setContextNotification(''), 3000);
  };
  
  // Manejador para el selector de key
  const handleKeyProviderChange = async (newProvider) => {
    clearTimeout(keyNotificationTimerRef.current);
    setKeyProvider(newProvider);
    await window.electronAPI.saveSeleccion(newProvider);
    loadKey(newProvider);
    
    const providerName = KEY_PROVIDER_CONFIG[newProvider].name;
    setKeyNotification(` Se ha fijado ${providerName} como proveedor de IA`);
    keyNotificationTimerRef.current = setTimeout(() => setKeyNotification(''), 3000);
  };

  // --- OTRAS FUNCIONES ---

  const restartApp = () => onCloseRestartModal();
  const openExternalLink = (url) => window.electronAPI.openExternalLink(url);
  
  const handleVersionCheck = async () => {
    setVersionTitle('Verificando versin...');
    setVersionMessage('');
    setVersionButtonText('');
    try {
      const updateResult = await window.electronAPI.checkForUpdates();
      if (updateResult.updateAvailable) {
        setVersionTitle('Nueva versin disponible!');
        setVersionMessage(`Hay una nueva versin (${updateResult.latestVersion}) disponible para descargar.`);
        setVersionButtonText('Descargar ahora');
        setVersionButtonClass('download-button');
        setVersionButtonAction(() => () => {
          window.electronAPI.openDownloadLink(updateResult.downloadUrl);
          onCloseVersionModal();
        });
      } else {
        setVersionTitle('Versin actualizada');
        setVersionMessage('Tienes la ltima versin instalada.');
        setVersionButtonText('OK');
        setVersionButtonClass('ok-button');
        setVersionButtonAction(() => onCloseVersionModal);
      }
    } catch (error) {
      setVersionTitle('Error');
      setVersionMessage('No se pudo verificar la versin.');
      setVersionButtonText('Cerrar');
      setVersionButtonClass('close-button');
      setVersionButtonAction(() => onCloseVersionModal);
    }
  };

  const handleBackdropClick = (e, closeFunction) => {
    if (e.target === e.currentTarget) closeFunction();
  };
  
  const currentKeyProviderConfig = KEY_PROVIDER_CONFIG[keyProvider];
  const currentContextProviderConfig = KEY_PROVIDER_CONFIG[contextProvider];

  return (
    <>
      {/* Modal de Contexto */}
      {showContextModal && (
        <div className="modal-backdrop show" onClick={(e) => handleBackdropClick(e, onCloseContextModal)}>
          <div className="modal-content">
            <h2>Editar Contexto para {currentContextProviderConfig.name}</h2>
            
            {contextNotification && <div className="modal-notification">{contextNotification}</div>}
            
            <select value={contextProvider} onChange={(e) => handleContextProviderChange(e.target.value)} style={{ width: '100%', padding: '8px', marginBottom: '15px' }}>
              <option value="groq">Groq</option>
              <option value="gemini">Google Gemini</option>
              <option value="openai">OpenAI</option>
              
            </select>

            {contextoError && <div className="modal-error">{contextoError}</div>}
            <textarea value={contextoValue} onChange={(e) => setContextoValue(e.target.value)} disabled={contextoLoading} maxLength="15000" />
            <div className="modal-buttons">
              <button onClick={saveContexto} disabled={contextoLoading} className="save-button">Guardar</button>
              <button onClick={onCloseContextModal} className="close-button">Cerrar</button>
            </div>
          </div>
        </div>
      )}

      {/* Modal de Key */}
      {showKeyModal && (
        <div className="modal-backdrop show" onClick={(e) => handleBackdropClick(e, onCloseKeyModal)}>
          <div className="modal-content">
            <h2>Editar Key de {currentKeyProviderConfig.name}</h2>
            
            {keyNotification && <div className="modal-notification">{keyNotification}</div>}
            
            <select value={keyProvider} onChange={(e) => handleKeyProviderChange(e.target.value)} style={{ width: '100%', padding: '8px', marginBottom: '15px' }}>
              <option value="groq">Groq</option>
              <option value="openai">OpenAI</option>
              <option value="gemini">Google Gemini</option>
            </select>
            
            {keyError && <div className="modal-error">{keyError}</div>}
            <input type="text" value={keyValue} onChange={(e) => setKeyValue(e.target.value)} disabled={keyLoading} maxLength="200" />
            
            <div id="keyDescription">
              Crea tu clave API en{' '}
              <a href={currentKeyProviderConfig.url} target="_blank" rel="noopener noreferrer" onClick={(e) => { e.preventDefault(); openExternalLink(currentKeyProviderConfig.url); }}>
                {currentKeyProviderConfig.url}
              </a>
              {' '}y cpiala aqu.
            </div>
            
            <div className="modal-buttons">
              <button onClick={saveKey} disabled={keyLoading} className="save-button">Guardar</button>
              <button onClick={onCloseKeyModal} className="close-button">Cerrar</button>
            </div>
          </div>
        </div>
      )}

      {/* Modal de Reinicio */}
      {showRestartModal && (
         <div className="modal-backdrop show" onClick={(e) => handleBackdropClick(e, onCloseRestartModal)}>
           <div className="modal-content">
             <h2>Cambios Guardados</h2>
             <p>Ya se han aplicado los cambios</p>
             <div className="modal-buttons">
               <button onClick={restartApp}>Continuar</button>
             </div>
           </div>
         </div>
      )}

      {/* Modal de Verificacin de Versin */}
      {showVersionModal && (
        <div className="modal-backdrop show" onClick={(e) => handleBackdropClick(e, onCloseVersionModal)}>
          <div className="modal-content">
            <h2 id="versionModalTitle">{versionTitle}</h2>
            <p id="versionModalMessage">{versionMessage}</p>
            <div className="modal-buttons">
              <button id="versionModalButton" className={versionButtonClass} onClick={versionButtonAction}>
                {versionButtonText}
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Modals;