// src/Validador.jsx
import React, { createContext, useContext, useState, useEffect } from 'react';

// Crear el contexto
const ValidadorContext = createContext();

// Hook personalizado para usar el contexto
export const useValidador = () => {
  const context = useContext(ValidadorContext);
  if (!context) {
    throw new Error('useValidador debe ser usado dentro de un ValidadorProvider');
  }
  return context;
};

// Provider del contexto
export const ValidadorProvider = ({ children }) => {
  // Estados de validacin individuales
  const [conditionsAccepted, setConditionsAccepted] = useState(false);
  const [apiKeyValid, setApiKeyValid] = useState(false);
  
  // Estado global calculado automticamente
  const [appValidated, setAppValidated] = useState(false);
  
  // Estados de control
  const [validationInitialized, setValidationInitialized] = useState(false);
  const [isValidating, setIsValidating] = useState(false);

  // Funcin principal de validacin
  const validateAllRequirements = async () => {
    if (isValidating) return appValidated; // Evitar validaciones concurrentes
    
    setIsValidating(true);
    console.log(' [VALIDADOR] Verificando TODOS los requisitos...');
    
    try {
      if (!window.electronAPI) {
        console.error(' [VALIDADOR] electronAPI no disponible');
        setIsValidating(false);
        return false;
      }

      // Verificar condiciones
      console.log(' [VALIDADOR] Verificando condiciones...');
      const conditionsResult = await window.electronAPI.getCondiciones();
      const cuentaResult = await window.electronAPI.getCuenta();
      
      setConditionsAccepted(cuentaResult);

      // Verificar API Key
      console.log(' [VALIDADOR] Verificando API Key...');
      const currentKey = await window.electronAPI.getKey();
      console.log(' [VALIDADOR] Key obtenida (longitud):', currentKey?.length || 0);
      
      // Validar clave API

      const isKeyValid = currentKey && 
                        currentKey.trim() !== '' && 
                        !currentKey.includes(' ');

      console.log(' [VALIDADOR] Key vlida:', isKeyValid);
      setApiKeyValid(isKeyValid);

      // Resultado final
      const allValid = cuentaResult && isKeyValid;
      console.log(' [VALIDADOR] VALIDACIN FINAL:', allValid);
      console.log('   - Condiciones:', cuentaResult);
      console.log('   - API Key:', isKeyValid);

      setAppValidated(allValid);
      setIsValidating(false);
      return allValid;

    } catch (error) {
      console.error(' [VALIDADOR] Error en validacin completa:', error);
      setIsValidating(false);
      setAppValidated(false);
      return false;
    }
  };

  // Funcin para actualizar solo las condiciones
  const updateConditionsAccepted = async (accepted) => {
    console.log(' [VALIDADOR] Actualizando condiciones:', accepted);
    setConditionsAccepted(accepted);
    
    // Re-calcular validacin global
    const finalValidation = accepted && apiKeyValid;
    console.log(' [VALIDADOR] Recalculando validacin global:', finalValidation);
    setAppValidated(finalValidation);
    
    return finalValidation;
  };

  // Funcin para actualizar solo la API Key
  const updateApiKeyValid = async (valid) => {
    console.log(' [VALIDADOR] Actualizando API Key:', valid);
    setApiKeyValid(valid);
    
    // Re-calcular validacin global
    const finalValidation = conditionsAccepted && valid;
    console.log(' [VALIDADOR] Recalculando validacin global:', finalValidation);
    setAppValidated(finalValidation);
    
    return finalValidation;
  };

  // Funcin para forzar re-validacin completa
  const forceRevalidation = async () => {
    console.log(' [VALIDADOR] Forzando re-validacin completa...');
    return await validateAllRequirements();
  };

  // Funcin para resetear validaciones
  const resetValidations = () => {
    console.log(' [VALIDADOR] Reseteando validaciones...');
    setConditionsAccepted(false);
    setApiKeyValid(false);
    setAppValidated(false);
    setValidationInitialized(false);
  };

  // Effect para validacin inicial
  useEffect(() => {
    if (!validationInitialized && window.electronAPI) {
      console.log(' [VALIDADOR] Iniciando validacin inicial...');
      setValidationInitialized(true);
      
      // Delay pequeo para asegurar que Electron est listo
      setTimeout(() => {
        validateAllRequirements();
      }, 500);
    }
  }, [validationInitialized]);

  // Effect para debug - monitorear cambios en estados
  useEffect(() => {
    console.log(' [VALIDADOR] Estado actualizado:', {
      conditionsAccepted,
      apiKeyValid,
      appValidated,
      validationInitialized
    });
  }, [conditionsAccepted, apiKeyValid, appValidated, validationInitialized]);

  // Valor del contexto
  const contextValue = {
    // Estados
    conditionsAccepted,
    apiKeyValid,
    appValidated,
    validationInitialized,
    isValidating,
    
    // Funciones
    validateAllRequirements,
    updateConditionsAccepted,
    updateApiKeyValid,
    forceRevalidation,
    resetValidations
  };

  return (
    <ValidadorContext.Provider value={contextValue}>
      {children}
    </ValidadorContext.Provider>
  );
};