// src/components/Tutoriales.jsx
import React, { useState } from 'react';
import './Tutoriales.css';

const Tutoriales = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [hasError, setHasError] = useState(false);

  const handleIframeLoad = () => {
    setIsLoading(false);
    setHasError(false);
  };

  const handleIframeError = () => {
    setIsLoading(false);
    setHasError(true);
  };

  const recargarPagina = () => {
    setIsLoading(true);
    setHasError(false);
    // Forzar recarga del iframe
    const iframe = document.querySelector('.tutoriales-iframe');
    if (iframe) {
      iframe.src = iframe.src;
    }
  };

  return (
    <div className="tutoriales-container">
      {/* Preloader */}
      {isLoading && (
        <div className="tutoriales-preloader">
          <div className="preloader-content">
            <div className="spinner"></div>
            <h3>Cargando Tutoriales...</h3>
            <p>Por favor espera un momento</p>
          </div>
        </div>
      )}

      {/* Error */}
      {hasError && (
        <div className="tutoriales-error">
          <div className="error-content">
            <div className="error-icon"></div>
            <h3>Error al cargar los tutoriales</h3>
            <p>No se pudo conectar con la pgina de tutoriales.</p>
            <button className="retry-btn" onClick={recargarPagina}>
               Intentar de nuevo
            </button>
          </div>
        </div>
      )}

      {/* Iframe */}
      <iframe
        src="https://artechclick.com/wai-info/tutoriales/"
        className={`tutoriales-iframe ${isLoading ? 'loading' : ''}`}
        title="Tutoriales - Agente IA WhatsApp"
        frameBorder="0"
        allowFullScreen
        onLoad={handleIframeLoad}
        onError={handleIframeError}
      />
    </div>
  );
};

export default Tutoriales;