import React, { useState, useEffect, useRef } from 'react';
import './MensajeImagen.css'; // Asegrate de que este archivo CSS es el que te gusta

const MensajeImagen = () => {
    // --- ESTADOS DEL COMPONENTE ---
    const [agente, setAgente] = useState('1');
    const [activo, setActivo] = useState(false);
    const [cargando, setCargando] = useState(true);
    const [guardando, setGuardando] = useState(false);
    const [mensaje, setMensaje] = useState({ texto: '', tipo: '' });
    
    // Estados para mensajes
    const [mensajeAprobado, setMensajeAprobado] = useState('');
    const [archivosAprobado, setArchivosAprobado] = useState([]);
    const [mensajeRechazado, setMensajeRechazado] = useState('');
    const [archivosRechazado, setArchivosRechazado] = useState([]);
    
    // Estados para filtros
    const [filtros, setFiltros] = useState({});
    const [filtroActivo, setFiltroActivo] = useState(null);
    const [inputValue, setInputValue] = useState('');
    const [mostrarModalNuevoFiltro, setMostrarModalNuevoFiltro] = useState(false);
    const [nombreNuevoFiltro, setNombreNuevoFiltro] = useState('');
    
    // --- REFS ---
    const fileInputAprobadoRef = useRef(null);
    const fileInputRechazadoRef = useRef(null);
    const inputRef = useRef(null);
    const isInitialMount = useRef(true);

    // --- HOOKS DE EFECTO ---
    useEffect(() => {
        cargarDatos();
    }, []);

    useEffect(() => {
        if (isInitialMount.current) {
            isInitialMount.current = false;
            return;
        }
        const debounceSave = setTimeout(() => {
            window.electronAPI.saveFiltros(filtros).catch(error => {
                console.error('Error en auto-guardado:', error);
                mostrarMensaje('Error al guardar cambio de filtro', 'error');
            });
        }, 500);
        return () => clearTimeout(debounceSave);
    }, [filtros]);

    // --- FUNCIONES ---
    const cargarDatos = async () => {
        setCargando(true);
        try {
            const agenteActual = await window.electronAPI.getAgente();
            setAgente(agenteActual);

            const datosImagen = await window.electronAPI.getMensajeImagen();
            if (datosImagen) {
                setMensajeAprobado(datosImagen.mensaje?.aprobado?.text || '');
                setArchivosAprobado(datosImagen.mensaje?.aprobado?.files || []);
                setMensajeRechazado(datosImagen.mensaje?.rechazado?.text || '');
                setArchivosRechazado(datosImagen.mensaje?.rechazado?.files || []);
                setActivo(datosImagen.activo || false);
            }

            const datosFiltros = await window.electronAPI.getFiltros();
            if (datosFiltros && Object.keys(datosFiltros).length > 0) {
                setFiltros(datosFiltros);
                setFiltroActivo(Object.keys(datosFiltros)[0]);
            } else {
                setFiltros({});
                setFiltroActivo(null);
            }
        } catch (error) {
            console.error('Error al cargar configuracin:', error);
            mostrarMensaje('Error al cargar la configuracin', 'error');
        } finally {
            setCargando(false);
        }
    };

    const mostrarMensaje = (texto, tipo) => {
        setMensaje({ texto, tipo });
        setTimeout(() => setMensaje({ texto: '', tipo: '' }), 4000);
    };

    // --- FUNCIN PARA ABRIR TUTORIAL ---
    const abrirTutorial = () => {
        window.open('https://youtu.be/MdtTkgYOCr8', '_blank');
    };

    // --- MANIPULACIN DE FILTROS ---
    const crearNuevoFiltro = () => {
        setMostrarModalNuevoFiltro(true);
        setNombreNuevoFiltro('');
    };
    
    const confirmarNuevoFiltro = () => {
        if (!nombreNuevoFiltro?.trim()) {
            mostrarMensaje('Por favor ingresa un nombre para el filtro', 'warning'); return;
        }
        const nombreLimpio = nombreNuevoFiltro.trim();
        if (filtros[nombreLimpio]) {
            mostrarMensaje('Ya existe un filtro con ese nombre', 'warning'); return;
        }
        setFiltros(prev => ({ ...prev, [nombreLimpio]: [] }));
        setFiltroActivo(nombreLimpio);
        setMostrarModalNuevoFiltro(false);
        setNombreNuevoFiltro('');
    };

    const agregarPalabras = () => {
        if (!filtroActivo || !inputValue.trim()) return;
        const nuevasPalabras = inputValue.split(',').map(p => p.trim()).filter(p => p && !filtros[filtroActivo].includes(p));
        if (nuevasPalabras.length > 0) {
            setFiltros(prev => ({ ...prev, [filtroActivo]: [...prev[filtroActivo], ...nuevasPalabras]}));
        }
        setInputValue('');
        inputRef.current?.focus();
    };

    const handleKeyDown = (e) => { if (e.key === 'Enter' || e.key === ',') { e.preventDefault(); agregarPalabras(); }};

    const eliminarPalabra = (filtroNombre, palabra) => setFiltros(prev => ({ ...prev, [filtroNombre]: prev[filtroNombre].filter(p => p !== palabra)}));

    const eliminarFiltro = (nombre) => {
        if (window.confirm(`Eliminar el filtro "${nombre}" y todas sus palabras?`)) {
            const nuevosFiltros = { ...filtros };
            delete nuevosFiltros[nombre];
            setFiltros(nuevosFiltros);
            if (filtroActivo === nombre) {
                setFiltroActivo(Object.keys(nuevosFiltros)[0] || null);
            }
        }
    };

    const manejarSeleccionArchivos = (event, tipo) => {
        const files = Array.from(event.target.files);
        if (tipo === 'aprobado') setArchivosAprobado(prev => [...prev, ...files].slice(0, 10));
        else setArchivosRechazado(prev => [...prev, ...files].slice(0, 10));
        event.target.value = '';
    };

    const eliminarArchivo = (index, tipo) => {
        if (tipo === 'aprobado') setArchivosAprobado(prev => prev.filter((_, i) => i !== index));
        else setArchivosRechazado(prev => prev.filter((_, i) => i !== index));
    };

    const guardarConfiguracion = async () => {
        if (activo && Object.keys(filtros).length === 0) {
            mostrarMensaje('Debes configurar al menos un filtro si la funcin est activada.', 'warning'); return;
        }
        setGuardando(true);
        try {
            const convertirArchivos = (archivos) => Promise.all(archivos.map(file => {
                if (file instanceof File) {
                    return new Promise((resolve, reject) => {
                        const reader = new FileReader();
                        reader.onload = () => resolve({ name: file.name, type: file.type, arrayBuffer: reader.result });
                        reader.onerror = error => reject(error);
                        reader.readAsArrayBuffer(file);
                    });
                } return Promise.resolve(file);
            }));
            const [aprobados, rechazados] = await Promise.all([convertirArchivos(archivosAprobado), convertirArchivos(archivosRechazado)]);
            await window.electronAPI.saveMensajeImagen({ aprobado: { text: mensajeAprobado.trim(), files: aprobados }, rechazado: { text: mensajeRechazado.trim(), files: rechazados }}, activo, filtros);
            mostrarMensaje(' Configuracin guardada exitosamente', 'success');
        } catch (error) {
            console.error('Error al guardar:', error);
            mostrarMensaje(error.message || 'Error al guardar la configuracin', 'error');
        } finally { setGuardando(false); }
    };

    const obtenerIconoArchivo = (archivo) => {
        const tipo = archivo.type; const nombre = archivo.name;
        if (tipo?.startsWith('image/')) return ''; if (tipo?.startsWith('video/')) return '';
        if (tipo?.startsWith('audio/')) return ''; if (tipo === 'application/pdf' || nombre?.endsWith('.pdf')) return '';
        if (tipo?.includes('word') || nombre?.endsWith('.doc') || nombre?.endsWith('.docx')) return '';
        if (tipo?.includes('excel') || nombre?.endsWith('.xls') || nombre?.endsWith('.xlsx')) return '';
        if (tipo?.includes('powerpoint') || nombre?.endsWith('.ppt') || nombre?.endsWith('.pptx')) return '';
        return '';
    };

    if (cargando) {
        return (
            <div className="mensaje-imagen-container">
                <div className="loading-spinner"><div className="spinner"></div><p>Cargando datos...</p></div>
            </div>
        );
    }

    return (
        <div className="mensaje-imagen-container">
            <div className="mensaje-imagen-header">
                <h1> Reconocimiento de Imagen</h1>
                <div className="header-actions">
                    <button className="btn-tutorial" onClick={abrirTutorial} title="Ver tutorial en YouTube">
                         Ver Tutorial
                    </button>
                    <div className="agente-info">
                        <span className="agente-label">Agente:</span>
                        <span className="agente-numero">{agente}</span>
                    </div>
                </div>
            </div>

            {mensaje.texto && <div className={`mensaje-notificacion ${mensaje.tipo}`}>{mensaje.texto}</div>}

            <div className="mensaje-imagen-content">
                {mostrarModalNuevoFiltro && (
                    <div className="modal-overlay" onClick={() => setMostrarModalNuevoFiltro(false)}>
                        <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                            <h3>Crear nuevo filtro</h3>
                            <input type="text" className="input-text" placeholder="Nombre del filtro (ej: Documento, Vigencia, etc.)" value={nombreNuevoFiltro} onChange={(e) => setNombreNuevoFiltro(e.target.value)} onKeyPress={(e) => e.key === 'Enter' && confirmarNuevoFiltro()} autoFocus/>
                            <div className="modal-buttons">
                                <button className="btn-cancelar" onClick={() => setMostrarModalNuevoFiltro(false)}>Cancelar</button>
                                <button className="btn-confirmar" onClick={confirmarNuevoFiltro}>Crear filtro</button>
                            </div>
                        </div>
                    </div>
                )}

                <div className="configuracion-section">
                    <div className="switch-container">
                        <label className="switch"><input type="checkbox" checked={activo} onChange={(e) => setActivo(e.target.checked)} /><span className="slider round"></span></label>
                        <span className="switch-label">{activo ? 'Procesamiento de imgenes activado' : 'Procesamiento de imgenes desactivado'}</span>
                    </div>
                    <p className="descripcion-funcionalidad">Cuando est activado, las imgenes recibidas sern procesadas con OCR para extraer texto y verificar si cumplen con los filtros configurados.</p>
                </div>

                <div className="filtros-section">
                    <div className="section-header">
                        <h2 className="section-title"> Configuracin de Filtros</h2>
                        <button className="btn-nuevo-filtro" onClick={crearNuevoFiltro}> Nuevo Filtro</button>
                    </div>
                    <div className="filtros-explicacion">
                        <p> <strong>Cmo funcionan los filtros:</strong></p>
                        <ul>
                            <li>Cada filtro es una condicin que <strong>DEBE cumplirse</strong></li>
                            <li>Dentro de cada filtro, basta con que <strong>UNA palabra</strong> est presente</li>
                            <li>La imagen ser aprobada solo si <strong>TODOS los filtros</strong> se cumplen</li>
                        </ul>
                    </div>

                    {Object.keys(filtros).length === 0 ? (
                        <div className="filtros-vacio"><p>No hay filtros configurados. Haz clic en "Nuevo Filtro" para comenzar.</p></div>
                    ) : (
                        <div className="filtros-tabs">
                            <div className="tabs-header">
                                {Object.keys(filtros).map(nombre => (
                                    <div key={nombre} className={`tab ${filtroActivo === nombre ? 'active' : ''}`} onClick={() => setFiltroActivo(nombre)}>
                                        <span>{nombre}</span>
                                        <button className="btn-eliminar-tab" onClick={(e) => { e.stopPropagation(); eliminarFiltro(nombre); }} title="Eliminar filtro"></button>
                                    </div>
                                ))}
                            </div>
                            {filtroActivo && filtros[filtroActivo] && (
                                <div className="tab-content">
                                    <div className="input-tags-container">
                                        <input ref={inputRef} type="text" className="input-tags" placeholder="Escribe palabras separadas por comas y presiona Enter..." value={inputValue} onChange={(e) => setInputValue(e.target.value)} onKeyDown={handleKeyDown}/>
                                        <button className="btn-agregar-palabras" onClick={agregarPalabras}>Agregar</button>
                                    </div>
                                    <div className="tags-container">
                                        {filtros[filtroActivo].length === 0 ? (
                                            <p className="sin-palabras">No hay palabras en este filtro. Agrega algunas arriba.</p>
                                        ) : (
                                            filtros[filtroActivo].map((palabra, index) => (
                                                <div key={`${palabra}-${index}`} className="tag">
                                                    <span>{palabra}</span>
                                                    <button className="tag-remove" onClick={() => eliminarPalabra(filtroActivo, palabra)} title="Eliminar palabra"></button>
                                                </div>
                                            ))
                                        )}
                                    </div>
                                    <div className="filtro-info">
                                        <span className="contador-palabras">{filtros[filtroActivo].length} palabra{filtros[filtroActivo].length !== 1 ? 's' : ''} en este filtro</span>
                                    </div>
                                </div>
                            )}
                        </div>
                    )}
                </div>

                <div className="mensajes-grid">
                    <div className="mensaje-section aprobado">
                        <div className="mensaje-header"><h3> Mensaje cuando APRUEBA</h3><span className="mensaje-descripcion">Se enviar cuando la imagen pase todos los filtros</span></div>
                        <textarea className="mensaje-textarea" placeholder="Escribe el mensaje que se enviar cuando la imagen sea aprobada..." value={mensajeAprobado} onChange={(e) => setMensajeAprobado(e.target.value)} rows={4}/>
                        <div className="archivos-subsection">
                            <div className="archivos-header">
                                <span className="archivos-titulo">Archivos adjuntos ({archivosAprobado.length}/10)</span>
                                <button className="btn-adjuntar" onClick={() => fileInputAprobadoRef.current?.click()} disabled={archivosAprobado.length >= 10}> Adjuntar</button>
                                <input ref={fileInputAprobadoRef} type="file" multiple accept="image/*,video/*,audio/*,.pdf,.doc,.docx,.xls,.xlsx,.ppt,.pptx,.txt" onChange={(e) => manejarSeleccionArchivos(e, 'aprobado')} style={{ display: 'none' }}/>
                            </div>
                            {archivosAprobado.length > 0 && (
                                <div className="archivos-lista">
                                    {archivosAprobado.map((archivo, index) => (
                                        <div key={index} className="archivo-chip">
                                            <span className="archivo-icono">{obtenerIconoArchivo(archivo)}</span>
                                            <span className="archivo-nombre" title={archivo.name}>{archivo.name}</span>
                                            <button className="archivo-eliminar" onClick={() => eliminarArchivo(index, 'aprobado')} title="Eliminar archivo"></button>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    </div>
                    <div className="mensaje-section rechazado">
                        <div className="mensaje-header"><h3> Mensaje cuando RECHAZA</h3><span className="mensaje-descripcion">Se enviar cuando la imagen NO pase los filtros</span></div>
                        <textarea className="mensaje-textarea" placeholder="Escribe el mensaje que se enviar cuando la imagen sea rechazada..." value={mensajeRechazado} onChange={(e) => setMensajeRechazado(e.target.value)} rows={4}/>
                        <div className="archivos-subsection">
                            <div className="archivos-header">
                                <span className="archivos-titulo">Archivos adjuntos ({archivosRechazado.length}/10)</span>
                                <button className="btn-adjuntar" onClick={() => fileInputRechazadoRef.current?.click()} disabled={archivosRechazado.length >= 10}> Adjuntar</button>
                                <input ref={fileInputRechazadoRef} type="file" multiple accept="image/*,video/*,audio/*,.pdf,.doc,.docx,.xls,.xlsx,.ppt,.pptx,.txt" onChange={(e) => manejarSeleccionArchivos(e, 'rechazado')} style={{ display: 'none' }}/>
                            </div>
                            {archivosRechazado.length > 0 && (
                                <div className="archivos-lista">
                                    {archivosRechazado.map((archivo, index) => (
                                        <div key={index} className="archivo-chip">
                                            <span className="archivo-icono">{obtenerIconoArchivo(archivo)}</span>
                                            <span className="archivo-nombre" title={archivo.name}>{archivo.name}</span>
                                            <button className="archivo-eliminar" onClick={() => eliminarArchivo(index, 'rechazado')} title="Eliminar archivo"></button>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    </div>
                </div>

                <div className="ejemplo-section">
                    <h3> Ejemplo de uso:</h3>
                    <div className="ejemplo-contenido">
                        <p>Si configuras:</p>
                        <ul>
                            <li><strong>Filtro "Documento":</strong> dni, cdula, documento, identificacin</li>
                            <li><strong>Filtro "Vigencia":</strong> 2024, 2025, vigente, vlido</li>
                        </ul>
                        <p> La imagen ser <strong>APROBADA</strong> si contiene (dni O cdula O ...) <strong>Y</strong> (2024 O 2025 O ...)</p>
                        <p> La imagen ser <strong>RECHAZADA</strong> si le falta alguno de los filtros</p>
                    </div>
                </div>

                <div className="acciones-section">
                    <button className="guardar-btn" onClick={guardarConfiguracion} disabled={guardando}>
                        {guardando ? (<><span className="btn-spinner"></span>Guardando...</>) : ' Guardar configuracin'}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default MensajeImagen;